import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../../../environments/environment';

/* Services */
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import * as algoliasearch from 'algoliasearch'

@Component({
	selector: 'app-header-algolia',
	templateUrl: './header-algolia.component.html',
	styleUrls: ['./header-algolia.component.scss'],
	providers: [],
})
export class HeaderAlgoliaComponent implements OnInit {

	@Input() searchWord     : string
	client              	= algoliasearch('XQX5JQG4ZD', '8be065c7ce07e14525c377668a190cf8');
	Project                 : any
	Profile                 : any
	PAndP 					: any
	projects                : Object[]
	profiles                : Object[]

	constructor(private PicturesService: PicturesService,
		private ProfilesService: ProfilesService,
		private router: Router) {
		this.Profile  	= this.client.initIndex('Users');
		this.Project 	= this.client.initIndex('Projects');
	}

	ngOnInit() {
		this.searchPAndP();
	}

	ngOnChanges(changes: SimpleChanges) {
		if (this.searchWord) {
			this.searchProjects(this.searchWord);
			this.searchProfiles(this.searchWord);
		}
	}

	searchPAndP() {

	}

	searchProjects(word) {
		this.Project.search(word, (err, content) => {
			this.projects = content.hits
		});
	}

	searchProfiles(word) {
		this.Profile.search(word, (err, content) => {
			this.profiles = content.hits
		});
	}

	goToProfile(id) {
		this.ProfilesService.getProfile(id).subscribe( res => {
			if (res.profile)
				this.router.navigate(['/', res.profile.username]);
		});
	}

	/* TRANSFORM */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}



