///<reference path="../../../../../node_modules/ng2-validation/dist/number/validator.d.ts"/>
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { DialogService } from 'ng2-bootstrap-modal';
import { TokenService } from '../../../Services/Authentication/token.service';
import { SocketService } from '../../../Services/Socket/socket.service';
import { SharedService } from '../../../Services/shared.service';
/* Components */
import { SearchModalUserComponent } from '../../modals/search-modal-user/search-modal-user.component';
import { MessageModalComponent } from '../../modals/message-modal/message-modal.component';

@Component({
	selector:    'app-header-chat',
	templateUrl: './header-chat.component.html',
	styleUrls:   ['./header-chat.component.scss']
})
export class HeaderChatComponent implements OnInit {


	@Output() unread: EventEmitter<any> = new EventEmitter<any>();
	discussions: Object[];
	unread_count: number;
	user: number;
	chat: any = {
		user: {}
	};
	hello: any

	constructor(private dialogService: DialogService,
		private SocketService: SocketService,
		private TokenService: TokenService,
		private SharedService: SharedService) {
		this.SocketService.parentFrameChangeEvent.subscribe(() => {
			setTimeout(() => {
				this.getDiscussions();
			}, 500);
		})
	}

	ngOnInit() {
		this.watchRead();
		this.getToken();
		this.getDiscussions();
	}

	watchRead() {
		this.SharedService.socketReload$.subscribe( boolean => {
			if (boolean)
				this.getDiscussions();
		});
	}

	getToken() {
		this.TokenService.getTokenStream()
		.map(token => token)
		.subscribe(token => {
			if (token)
				this.user = token.user.id
		});
	}

	getDiscussions() {
		this.SocketService.discussions()
		.subscribe(discussions => {
			this.discussions = discussions.discussions;
			this.unread_count = this.discussions
			.filter((d: any) => !(d.read as boolean))
			.length;
			this.unread.emit(this.unread_count)
		});
	}

	checkExistRoom(id, list) {
		if (!list)
			return false

		const length 	= list.length
		let check1 		= false
		let check2 		= false

		for (var i = 0; i < length; i++) {
			for (var c = 0; c < list[i].members.length; c++) {
				if (id === list[i].members[c].uid || this.user === list[i].members[c].uid) {
					if (id === list[i].members[c].uid)
						check1 = true
					if (this.user === list[i].members[c].uid)
						check2 = true
				}
			}
			if (check1 && check2)
				return list[i].members[0].room_id
			else
				check1 = check2 = false
		}
		return false
	}

	/* SHOW MODAL */
	showSearchUserModal(id: number) {
		let disposable = this.dialogService.addDialog(SearchModalUserComponent, {
			called_id: id
		}).subscribe(profile => {
			if (profile)
				this.showMessageModal(profile)
		});
	}

	showMessageModal(profile: Object) {
		let disposable = this.dialogService.addDialog(MessageModalComponent, {
			profile 	: profile,
			roomId 		: this.checkExistRoom(profile['uid'], this.discussions)
		}).subscribe(success => {
			if (success)
				this.getDiscussions();
		});
	}
}



