import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

/* Components */

/* Directives */
import { GooglePlaceDirective } from './google-place.directive';
import { NeedModalDirective } from './Modals/need.modal.directive';
import { AboutModalDirective } from './Modals/about.modal.directive';
import { CreateProjectDirective } from './Create-project/create-project.directive';
import { SignupDirective } from './Signup/signup.directive';
import { SettingDirective } from './Settings/setting.directive';

@NgModule({
	imports: [
		CommonModule,
	],
	declarations: [
		GooglePlaceDirective,
		NeedModalDirective,
		AboutModalDirective,
		CreateProjectDirective,
		SignupDirective,
		SettingDirective
	],
	exports: [
		GooglePlaceDirective,
		NeedModalDirective,
		AboutModalDirective,
		CreateProjectDirective,
		SignupDirective,
		SettingDirective
	]
})
export class DirectivesModule { }


