/*** CUSTOM ANIMATIONS ***/
import { trigger, state, animate, style, transition, keyframes, AnimationEntryMetadata } from '@angular/core';

// export function customAnim():AnimationEntryMetadata {
// 	return delayTransition();
// }

/* Message Mobile Animation */
export const slideRightMobile = trigger('slideRightNav', [
			state('on', style({
	    		right : '0px'
	    	})),
	    	transition('* <=> *', animate('150ms')),
	    ])

export const slideRightMessageMobile = trigger('slideRightMessageModal', [
			state('in', style({
	    		right : '0px'
	    	})),
	    	state('out', style({
	    		right : '-1000px'
	    	})),
	    	transition('* <=> *', animate('200ms')),
	    ])

/* Project animation */
export const projectAnim = trigger('slide', [
			state('inactive', style({
				maxHeight: '105px', overflow: 'hidden'
			})),
			state('active',   style({
				maxHeight: '10000px', overflow: 'auto'
			})),
			transition('inactive => active', animate('800ms ease-in')),
			transition('active => inactive', animate('800ms ease-out'))
	    ]);

export const projectAnim2 = trigger('slideFilter', [
			state('inactive', style({
				height: '105px', overflow: 'hidden'
			})),
			state('active',   style({
				height: '1000px', overflow: 'auto'
			})),
			transition('inactive => active', animate('800ms ease-in')),
			transition('active => inactive', animate('800ms ease-out'))
	    ]);

export const projectAnim3 = trigger('rotate', [
	    	state('down', style({
	    		transform: 'rotate(0)'
	    	})),
	    	state('up', style({
	    		transform: 'rotate(180deg)'
	    	})),
	    	transition('inactive => active', animate(200)),
			transition('active => inactive', animate(200))
	    ]);

/* Popover Dialog */
export const dialogPopover = trigger('popover', [
			transition('void => *', [
				style({ bottom: '-250px', opacity: 0}),
				animate("150ms")
			]),
			transition('* => void', [
				animate("150ms", style({ bottom: '-250px' , opacity: 0}))
			])
		]);

/* Discoer Popover Dialog */
export const popDiscover = trigger('popover', [
			state('up1', style({
				bottom: '0px',
				padding: '20px 15px 25px 15px'
			})),
			state('up2', style({
				bottom: '0px',
				padding: '45px 15px'
			})),
			state('up3', style({
				bottom: '0px',
				padding: '60px 15px'
			})),
			transition('* => *', animate("450ms"))
		])

/* Slide down/up dialog */
export const dialog = trigger('dialog', [
		transition('void => *', [
			style({ top: "38%" }),
			animate("200ms")
		]),
		transition('* => void', [
			animate("200ms", style({ top: '38%'}))
		])
	]);

/* Mobile dialog */
export const mobileDialog = trigger('mDialog', [
  state('down', style({
    top: '0', opacity: '1'
  })),
  state('up', style({
    top: '-50px', opacity: '0'
  })),
  transition('down => up', animate('200ms')),
  transition('up => down', animate('200ms'))
]);

/* Display page */
export const fadeInPage = trigger('fadeInPage', [
			transition('void => *', [
				animate("100ms", keyframes([
					style({ opacity: 0, offset: 0 }),
          style({opacity: 0.6, offset: 1})
				]))
			]),
			transition('* => void', [
				animate("100ms"),
				style({ opacity: 0})
			])
		])

/* Display filter */
export const background = trigger('background', [
			transition('void => *', [
				animate("50ms", keyframes([
					style({ opacity: 0, offset: 0 }),
          style({opacity: 0.6, offset: 1})
				]))
			]),
			transition('* => void', [
				animate("50ms"),
				style({ opacity: 0})
			])
		])

/* Display filter 2 */
export const background2 = trigger('background2', [
		state('fadeOut', style({
			opacity: 0,
		})),
		state('fadeIn', style({
	    	opacity: 1,
	    })),
	    transition('void => *', animate('250ms')),
		transition('fadeIn => fadeOut', animate('250ms')),
	    transition('fadeOut => fadeIn', animate('250ms')),
	])

/* Bounce Ease Repeat */
export const noDelay 	= '800ms',
	delay1 		= '800ms 100ms',
	delay2 		= '800ms 250ms'

// export const bounceIn = (params) => animate(params, keyframes([
// 					style({ top: "800px", offset: 0 }),
// 					style({ top: "-30px", offset: 0.3 }),
// 					style({ top: "20px", offset: 0.55 }),
// 					style({ top: "-10px", offset: 0.75 }),
// 					style({ top: "0px", offset: 1 })
// 				]))

export const bounceEaseRepeat = trigger('bounceER', [
		state('initial', style({
			opacity: 0, top: '800px'
		})),
		state('bounceIn', style({
			opacity: 1, top: '0px'
		})),
		state('bounceInDelay1', style({
			opacity: 1, top: '0px'
		})),
		state('bounceInDelay2', style({
			opacity: 1, top: '0px'
		})),
		state('fadeOut', style({
			opacity: 0,
		})),

		transition('void => bounceIn', animate(noDelay, keyframes([
					style({ top: "800px", offset: 0 }),
					style({ top: "-30px", offset: 0.3 }),
					style({ top: "20px", offset: 0.55 }),
					style({ top: "-10px", offset: 0.75 }),
					style({ top: "0px", offset: 1 })
				]))),
		transition('void => bounceInDelay1', animate(delay1, keyframes([
					style({ top: "800px", offset: 0 }),
					style({ top: "-30px", offset: 0.3 }),
					style({ top: "20px", offset: 0.55 }),
					style({ top: "-10px", offset: 0.75 }),
					style({ top: "0px", offset: 1 })
				]))),
		transition('void => bounceInDelay2', animate(delay2, keyframes([
					style({ top: "800px", offset: 0 }),
					style({ top: "-30px", offset: 0.3 }),
					style({ top: "20px", offset: 0.55 }),
					style({ top: "-10px", offset: 0.75 }),
					style({ top: "0px", offset: 1 })
				]))),
		transition('bounceInDelay2 => fadeOut', animate('250ms')),
		transition('* => initial', animate('250ms'))

	])

/* Slide Right/Left dialog */
export const slideLeft = trigger('slideLeft', [
	    	state('out', style({
	    		display: 'none', left: '-10000px'
	    	})),
	    	state('in', style({
	    		left: '0px'
	    	})),
	    	transition('out => in', animate('300ms')),
			transition('in => out', animate('300ms'))
	    ]);

export const slideRight = trigger('slideRight', [
	    	state('out', style({
	    		display: 'none', right: '-10000px'
	    	})),
	    	state('in', style({
	    		right: '0px'
	    	})),
	    	transition('out => in', animate('300ms')),
			transition('in => out', animate('300ms'))
	    ]);

/* Slide Right/Left */

export const slideActivities = trigger('slideLeft', [
			state('left1', style({
	    		marginLeft : '0px'
	    	})),
	    	state('left2', style({
	    		marginLeft : '-250px'
	    	})),
	    	state('left3', style({
	    		marginLeft: '-500px'
	    	})),
	    	state('left4', style({
	    		marginLeft: '-750px'
	    	})),
	    	transition('* <=> *', animate('300ms')),
	    ]);

export const inOutText = trigger('inOutText', [
			transition('* => *', [
				animate(300, keyframes([
						style({opacity: 0, offset: 0}),
						style({opacity: 1, offset: 1})
					]))
				])
	    ]);

/* Profile Animation */
export const profileAnime1 = trigger('slide', [
			state('inactive', style({
				maxHeight: '0px', overflowY: 'hidden'
			})),
			state('active',   style({
				maxHeight: '2000px', overflowY: 'hidden'
			})),
			transition('inactive => active', animate('300ms')),
			transition('active => inactive', animate('300ms'))
	    ]);

export const profileAnime2 = trigger('rotate', [
	    	state('down', style({
	    		transform: 'rotate(0)'
	    	})),
	    	state('up', style({
	    		transform: 'rotate(180deg)'
	    	})),
	    	transition('inactive => active', animate('300ms')),
			transition('active => inactive', animate('300ms'))
	    ]);



