import { Directive, ElementRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Directive({
	selector: '[headerDiv]',
	host: {
		'(document:click)': 'onClick($event)',
	},
})
export class HeaderDirective {

	nativeElement 	: any
	target 			: any
	isWhite 		: boolean = false

	constructor(private el: ElementRef,
				private router: Router) {
		this.nativeElement = el.nativeElement
		this.checkPathChangeHeader();
	}

	checkPathChangeHeader() {
		this.router.events.subscribe((event) => {
			if(event instanceof NavigationEnd) {
				if (event.url === '/users/login' || event.url === '/users/register' ||
					event.url.indexOf('/admin/panel') >= 0) {
					this.isWhite = true;
				} else {
					this.isWhite = false;
				}
			}
		});;
	}

	/**** Arrow Rotate ****/
	showNav() {
		if (this.nativeElement.querySelector('.whl-nav').style.display !== 'block') {
			this.nativeElement.querySelector('.whl-img1').style.transform = 'rotate(180deg)'
			this.nativeElement.querySelector('.whl-nav').style.display = 'block'
			this.hideNotif();
			this.hideChat();
		} else
			this.hideNav();
	}

	hideNav() {
		this.nativeElement.querySelector('.whl-nav').style.display = 'none'
		this.nativeElement.querySelector('.whl-img1').style.transform = 'rotate(0)'
	}

	/**** Chat ****/
	showChat() {
		if (this.nativeElement.querySelector('#whl5-1-ahc').style.display !== 'block') {
			this.nativeElement.querySelector('#whl5-1-ahc').style.display = 'block'
			this.hideNav();
			this.hideNotif();
		} else
			this.hideChat();
	}

	hideChat() {
		if (!this.nativeElement.querySelector('#whl5-1-ahc').contains(this.target))
			this.nativeElement.querySelector('#whl5-1-ahc').style.display = 'none'
	}

	/**** Notif ****/
	showNotif() {
		if (this.nativeElement.querySelector('#whl5-1-ahn').style.display !== 'block') {
			this.nativeElement.querySelector('#whl5-1-ahn').style.display = 'block'
			this.hideNav();
			this.hideChat();
		} else
			this.hideNotif();
	}

	hideNotif() {
		if (!this.nativeElement.querySelector('#whl5-1-ahn').contains(this.target))
			this.nativeElement.querySelector('#whl5-1-ahn').style.display = 'none'
	}

	/**** Default Header ****/
	switchToDefaultHeader() {
		if (this.nativeElement.querySelector('#whalgo').style.display !== 'flex' || this.isWhite) {
			this.nativeElement.querySelector('#whalgo').style.display 		= 'none'
			return ;
		}

		const image1 = '/public/images/new-logo.svg'
		const image2 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/waves-icon-w_wslyzh.png'
		const image3 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/mailbox-icon-w_sji3lw.png'
		const image4 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/arrow-down-icon-w_csniet.svg'

		this.nativeElement.querySelector('#whl1-i').src 					= image1
		this.nativeElement.querySelector('#whl1').className 				= 'wh-link pad-l-20'
		this.nativeElement.querySelector('#whl2').className 				= 'wh-button'
		this.nativeElement.parentNode.querySelector('#w-header').className 	= 'transition-200'
		this.nativeElement.querySelector('#whalgo').style.display 			= 'none'

		if (this.nativeElement.querySelector('#whl5-1-i')) {
			this.nativeElement.querySelector('#whl5-1-i').src 				= image2
			this.nativeElement.querySelector('#whl5-2-i').src 				= image3
			this.nativeElement.querySelector('#whl5-3-i').src 				= image4
			this.nativeElement.querySelector('#whl5-3').className 			= 'pad-x-15 log-picture cursor-pt'
		} else
			this.nativeElement.querySelector('#whl4').className 			+= 'wh-log pad-r-10'
	}


	onClick(event) {
		if (!this.nativeElement.parentNode.querySelector('#w-header').contains(event.target)) {
			this.switchToDefaultHeader();
		} else {
			if ((this.nativeElement.querySelector('#whalprr') && this.nativeElement.querySelector('#whalprr').contains(event.target))
				|| (this.nativeElement.querySelector('#whalper') && this.nativeElement.querySelector('#whalper').contains(event.target))
				|| (this.nativeElement.querySelector('#whalnr') && this.nativeElement.querySelector('#whalnr').contains(event.target))) {
				this.switchToDefaultHeader();
			}
		}

		if (this.nativeElement.querySelector('#whl5')) {
			if (event.target.id == 'bubble' || this.nativeElement.querySelector('#whl5-1').contains(event.target)) {
				this.showNotif();
			} else if (event.target.id == 'bubble1' || this.nativeElement.querySelector('#whl5-2').contains(event.target)) {
				this.showChat();
			} else if (this.nativeElement.querySelector('#whl5-3').contains(event.target)) {
				this.showNav();
			} else {
				this.hideNav();
				this.hideChat();
				this.hideNotif();
			}
		}
	}

}



