import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';

/* Animations */
import { background } from '../../../Animations/animations';
import { newDialog } from '../../../Animations/slides3.animation';

export interface ConfirmModel {
	selectedSkills: Array<string>
}

@Component({
	selector: 'app-skill-modal',
	templateUrl: './skill-modal.component.html',
	styleUrls: ['./skill-modal.component.scss'],
	animations: [newDialog, background],
	providers: [SkillsService]
})
export class SkillModalComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	state 				: string = 'outUp'
	selectedSkills 		: Array<string> = []
	skills 				: Object[]
	categories 			: Object[]
	currentCategory 	: string
	skillSearch			: string
	showDisplay 		: boolean = false

	constructor(dialogService: DialogService,
				private SkillsService: SkillsService) {
		super(dialogService)
	}

	ngOnInit() {
		this.initServices();
	}

	initServices() {
		this.getSkills();
		this.getSkillCategories();
	}

	getSkills() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills 		= res.skills
			this.showDisplay 	= true
			setTimeout( () => {
				this.state 		= 'initial'
			}, 200)
		});
	}

	getSkillCategories() {
		this.SkillsService.getSkillCategories().subscribe( res => {
			this.categories 	= res.categories
		});
	}

	getSkillsByCategories(category) {
		this.currentCategory = category.name;
		this.SkillsService.getSkillsByCategories({ id: category.id }).subscribe( res => {
			this.skills = res.skills
		})
	}

	checkSkill(skill) {
		if (this.selectedSkills.length > 4 || this.selectedSkills.indexOf(skill) >= 0)
			return false
		else
			return true
	}

	addSkill(skill) {
		if (this.checkSkill(skill))
			this.selectedSkills.push(skill);
	}

	removeSkill(index, id) {
		this.selectedSkills.splice(index, 1)
	}

	hideModal() {
		this.state 	= 'outUp'
		this.result = this.selectedSkills
		this.close();
	}

}



