import { Injectable, EventEmitter } from '@angular/core';
import * as io from 'socket.io-client';
import { Token } from '../Authentication/token.model';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { TokenService } from '../Authentication/token.service';

@Injectable()
export class SocketService {
	public parentFrameChangeEvent: EventEmitter<any>;
	private url: string = environment.socket;
	private socket;
	private connected: boolean;
	private authenticated: boolean;
	private auth: Token;

	constructor(private token: TokenService) {
		this.socket = io(this.url);

		this._fetch('error')
		.subscribe(error => console.log(error));

		this._fetch('connect')
		.subscribe(connect => {
			this.token.getTokenStream()
			.subscribe(token => {
				this.auth = token;
				if (this.auth === null) {
					this.connected = false;
					this.authenticated = false;
				} else {
					this.authenticate(this.auth)
				}
			})
		});

		this._fetch('server::authentication')
		.subscribe(status => {
			// console.log('server::authentication', status);
			this.socket.authenticated = status.code === 200;
		});

		this.parentFrameChangeEvent = new EventEmitter<any>();
	}

	public authenticate(token: Token) {
		this.socket.emit('client::authenticate', {
			token: {
				rest: token.auth.token
			}
		});
	}

	public fetchMessages(id) {
		this.socket.emit('client::messages::fetch', {id: id});
		return this._fetch('server::data::messages')
	}

	public discussions(): Observable<any> {
		this.socket.emit('client::discussions');
		return this._fetch('server::data::discussions');
	}

	public queryUser(id): Observable<any> {
		this.socket.emit('client::request:user', {id: id});
		return this._fetch('server::resources::user')
	}

	public fetch(route): Observable<any> {
		return this._fetch(route)
	}

	public autocomplete(resource): Observable<any> {
		return this._fetch(`server::autocomplete::${resource}`);
	}

	public status(): Observable<any> {
		return this._fetch('server::authentication')
	}

	public notification(): Observable<any> {
		this.socket.emit('client::notifications::fetch');
		return this._fetch('server::notification')
	}

	public recentActivities(options?: any): Observable<any> {
		this.socket.emit('client::activities', options);
		return this._fetch('server::recent::activities')
	}

	public notificationMarkAsRead(id?: number) {
		this.socket.emit('client::notification::mark_as_read', id)
	}

	public messagesMarkAsRead(id: number) {
		this.socket.emit('client::message::mark_as_read', {id: id});
	}

	public sendMessage(data) {
		this.socket.emit('client::messages', {
			message: data
		});
		this.parentFrameChangeEvent.emit();
	}

	_fetch(route): Observable<any> {
		return Observable.create(o => {
			this.socket.on(route, data => o.next(data));
			return {
				dispose: this.socket.close
			}
		})
	}
}



