import { Component, OnInit, trigger, state, style, transition, animate, keyframes } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { ExperiencesService } from '../../../Services/Experiences/experiences.service';
import { TokenService } from '../../../Services/Authentication/token.service';

/* Interface */
import { months } from '../../../Interfaces/Constants/time-constant';
import { experience } from '../../../Interfaces/Constants/experience-constant';
import { ExperienceModel, ExperienceConfirmModel } from '../../../Interfaces/experience-models';

/* Animations */
import { dialog, background } from '../../../Animations/animations';

@Component({
	selector: 'app-experience-modal',
	templateUrl: './experience-modal.component.html',
	styleUrls: ['./experience-modal.component.scss'],
	animations: [dialog, background],
	providers: [
		ExperiencesService,
	]
})
export class ExperienceModalComponent extends DialogComponent<ExperienceConfirmModel, boolean> implements OnInit {

	index 				: number
	user_experience		: any
	months				: any
	time 	 			: any
	years 				: Array<number> = []
	experience 			: any
	user 				: any
	location 			: string
	buttonText 			: string
	currentMonthFrom 	: string
	currentMonthTo		: string
	currentYearFrom		: string
	currentYearTo		: string
	currentWork 		: boolean
	showDisplay 		: boolean = false
	show1 				: boolean = false
	show2 				: boolean = false
	show3 				: boolean = false
	show4 				: boolean = false
	title_err 			: boolean = false
	company_err 		: boolean = false
	location_err 		: boolean = false
	time_err 			: boolean = false

	constructor(dialogService: DialogService,
				private ExperiencesService: ExperiencesService,
				private TokenService: TokenService) {
		super(dialogService)
	}

	ngOnInit() {
		this.initVariables();
		if (this.TokenService.getToken())
			this.user = this.TokenService.getToken().user
		this.showDisplay = true
	}

	initVariables() {
		this.time 				= {}
		this.experience 		= {}
		this.currentMonthFrom 	= this.currentMonthTo 	= 'Month'
		this.currentYearFrom 	= this.currentYearTo 	= 'Year'
		this.buttonText 		= this.index < 0 ? 'Add position' : 'Edit position'
		this.months 			= months
		/* Init experience for edit */
		this.initExperienceToEdit();

		/* Init years */
		for (var i = 2017; i > 1949; i--) {
			this.years.push(i);
		}
	}

	initExperienceToEdit() {
		if (this.user_experience.title) {
			this.experience = {
				title				: this.user_experience.title,
				company 			: this.user_experience.company,
				description 		: this.user_experience.description,
				date_from 			: this.user_experience.date_from,
				date_to 			: this.user_experience.date_to
			}

			this.location 			= this.user_experience.location
			// if (this.experience.location_state)
			// 	this.location 		= this.experience.location_city + ', ' + this.experience.location_state
			// else
			// 	this.location 		= this.experience.location_city + ', ' + this.experience.location_country

			const date_from 		= new Date(this.experience.date_from)
			this.currentMonthFrom 	= this.months[date_from.getMonth()].name
			this.currentYearFrom 	= date_from.getFullYear().toString()

			if (this.experience.date_to === 'Present')
				this.currentWork 	= true
			else {
				const date_to 		= new Date(this.experience.date_to)
				this.currentMonthTo = this.months[date_to.getMonth()].name
				this.currentYearTo 	= date_to.getFullYear().toString()
			}
		}
	}

	validateExperience() {
		this.title_err = this.company_err = this.location_err = this.time_err = true

		if (this.experience.title)
			this.title_err 		= false
		if (this.experience.company)
			this.company_err 	= false
		if (this.location || this.experience.location)
			this.location_err 	= false
		if (this.experience.date_from)
			this.time_err 		= false

		return (this.title_err || this.company_err || this.location_err || this.time_err)
	}

	getAddress(place: Object) {
		const address = place['formatted_address'].replace('USA', 'United States')
		const location: Array<any> 			= address.split(', ');
		const lastIndex 					= location.length - 1

		this.experience.location 			= {
			city 		: location[0],
			state 		: location[1] && (lastIndex > 1) ? location[1] : '',
			country 	: location[lastIndex] ? location[lastIndex] : '',
			lng 		: place['geometry'].location.lat(),
 			lat 		: place['geometry'].location.lng()
 		}
	}

	selectTimes(type, value, name) {
		this.show1 = this.show2 = this.show3 = this.show4 = false;
		this.time[type] = value
		if (type === 'monthFrom')
			this.currentMonthFrom 	= name
		else if (type === 'monthTo')
			this.currentMonthTo 	= name
		else if (type === 'yearFrom')
			this.currentYearFrom 	= value
		else if (type === 'yearTo')
			this.currentYearTo 		= value
	}

	openSelectTime(index) {
		if (index === 1) {
			this.show1 = this.show1 ? false : true
			this.show2 = this.show3 = this.show4 = false
		} else if ( index === 2) {
			this.show2 = this.show2 ? false : true
			this.show1 = this.show3 = this.show4 = false
		} else if ( index === 3) {
			this.show3 = this.show3 ? false : true
			this.show1 = this.show2 = this.show4 = false
		} else if ( index === 4) {
			this.show4 = this.show4 ? false : true
			this.show1 = this.show2 = this.show3 = false
		}
	}

	getDate() {
		this.experience.date_to = 'Present'
		const date_from: any 	= new Date(this.time.yearFrom, this.time.monthFrom - 1, 1);
		const date_to: any 		= new Date(this.time.yearTo, this.time.monthTo - 1, 1);

		if (this.currentWork)
			this.experience.date_to 	= "Present"
		if (!this.currentWork && !isNaN(date_to))
			this.experience.date_to 	= date_to
		if (!isNaN(date_from))
			this.experience.date_from 	= date_from
		return true
	}

	addUserExperience() {
		this.experience.description = this.replaceNewline(this.experience.description);

		this.ExperiencesService.addExperience(this.user.id, this.experience).subscribe( res => {
			if (res.success) {
				this.result = true
				this.hideModalSave();
			}
		});
	}

	editUserExperience() {
		this.experience.id 			= this.user_experience.id
		this.experience.description = this.replaceNewline(this.experience.description);

		this.ExperiencesService.updateExperience(this.user.id, this.experience).subscribe( res => {
			if (res.success) {
				this.result = true
				this.hideModalSave();
			}
		});
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	saveExperience() {
		if (this.getDate() && !this.validateExperience() && !this.user_experience.title) {
			this.addUserExperience();
		} else if (this.user_experience && !this.validateExperience()) {
			this.editUserExperience();
		} else
			return
	}

	hideModal() {
		this.showDisplay = false
		this.close();
	}

	hideModalSave() {
		this.buttonText = 'Saved'
		setTimeout( () => {
			this.showDisplay 	= false
			this.experience 	= {}
			this.hideModal();
		}, 1500)
	}

}



