import { Injectable, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';

@Injectable()
export class MetaService {

	constructor( @Inject(DOCUMENT) private document) {
	}

	getMeta(name: string): string {
		return this.document[name];
	}

	setMeta(name: string, value: string) {
		let metaTag1 	= this.document.querySelector('meta[name="' + name + '"]');
		let metaTag2 	= this.document.querySelector('meta[property="' + name + '"]')

		if (!metaTag1 && name.indexOf('og:') < 0) {
			metaTag1 = this.document.createElement('meta');
			metaTag1.setAttribute('name', name);
			document.getElementsByTagName('HEAD')[0].appendChild(metaTag1)
		}

		if (!metaTag2 && name.indexOf('og:') >= 0) {
			metaTag2 = this.document.createElement('meta');
			metaTag2.setAttribute('property', name);
			document.getElementsByTagName('HEAD')[0].appendChild(metaTag2)
		}

		if (name.indexOf('og:') < 0) {
			metaTag1.setAttribute('content', value);
		}

		if (name.indexOf('og:') >= 0) {
			metaTag2.setAttribute('content', value);
		}
	}
}



