import { Directive, ElementRef } from '@angular/core';

@Directive({
	selector: '[needModalDiv]',
	host: {
		'(document:click)' 	: 'onClick($event)',
	},
})
export class NeedModalDirective {

	nativeElement : any
	target : any

	constructor(private el: ElementRef) {
		this.nativeElement = el.nativeElement
	}

	rotateDown() {
		this.nativeElement.querySelector('#nmbd-1-i').style.transform = 'rotate(0)'
	}

	rotateUp() {
		this.nativeElement.querySelector('#nmbd-1-i').style.transform = 'rotate(180deg)'
	}

	showStatus() {
		if (this.nativeElement.querySelector('#nmbdb-1').style.display !== 'block') {
			this.nativeElement.querySelector('#nmbdb-1').style.display = 'block'
			this.rotateUp();
			// this.hideSkill();
		} else
			this.hideStatus();
	}

	hideStatus() {
		this.rotateDown();
		this.nativeElement.querySelector('#nmbdb-1').style.display = 'none'
	}

	// showSkill() {
	// 	this.nativeElement.querySelector('#nmbdb-2').style.display = 'block'
	// 	this.hideStatus();
	// }

	// hideSkill() {
	// 	if (this.nativeElement.querySelector('#nmbdb-2'))
	// 		this.nativeElement.querySelector('#nmbdb-2').style.display = 'none'
	// }


	onClick(event) {
		if (this.nativeElement.querySelector('#nmbd-1').contains(event.target)) {
			this.showStatus();
		} else if (this.nativeElement.querySelector('#nmbd-2-i') &&
			this.nativeElement.querySelector('#nmbd-2-i').contains(event.target)) {
			// this.showSkill();
		} else {
			this.hideStatus();
			// this.hideSkill();
		}
	}
}



