import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { SkillsService } from '../../../../Services/Skills/skills.service';
import { TokenService } from '../../../../Services/Authentication/token.service';

/* Animations */
import { background } from '../../../../Animations/animations';
import { newDialog } from '../../../../Animations/slides3.animation';

export interface ConfirmModel {
	user_id: number
}

@Component({
	selector: 'app-profile-edit-modal',
	templateUrl: './profile-skill.component.html',
	styleUrls: ['./profile-skill.component.scss'],
	animations: [newDialog, background],
	providers: [
		SkillsService,
	]
})
export class ProfileSkillComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	user_id 			: number
	state 				: string = 'outUp'
	user_skills 		: Object[]
	skills 				: Object[]
	categories 			: Object[]
	currentCategory 	: string
	skillSearch			: string
	showDisplay 		: boolean = false

	constructor(dialogService: DialogService,
				private SkillsService: SkillsService,
				private TokenService: TokenService) {
		super(dialogService)
	}

	ngOnInit() {
		// this.initToken();
		this.initServices();
	}

	// initToken() {
	// 	if (this.TokenService.getToken())
	// 		this.user = this.TokenService.getToken().user
	// }

	initServices() {
		this.getUserSkills();
		this.getSkills();
		this.getSkillCategories();
	}

	getUserSkills() {
		this.SkillsService.getUserSkills(this.user_id).subscribe(res => {
			this.user_skills = res.skills;
		});
	}

	getSkills() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills 		= res.skills
			this.showDisplay 	= true
			setTimeout( () => {
				this.state 		= 'initial'
			}, 200)
		});
	}

	getSkillCategories() {
		this.SkillsService.getSkillCategories().subscribe( res => {
			this.categories 	= res.categories
		});
	}

	getSkillsByCategories(category) {
		this.currentCategory = category.name;
		this.SkillsService.getSkillsByCategories({ id: category.id }).subscribe( res => {
			this.skills = res.skills
		})
	}

	addSkill(skill) {
		if (this.checkSkill(skill)) {
			this.SkillsService.addUserSkills(this.user_id, { skill_id: skill.id }).subscribe( res => {
				if (res.skills)
					this.user_skills.push(skill);
			});
		}
	}

	removeSkill(index, id) {
		this.SkillsService.deleteUserSkills(this.user_id, { skill_id: id }).subscribe( res => {
			if (res.success)
				this.user_skills.splice(index, 1)
		});
	}

	checkSkill(skill) {
		if (this.user_skills.indexOf(skill) >= 0)
			return false
		else
			return true
	}

	hideModal() {
		this.state 	= 'outUp'
		this.result = this.user_skills
		this.close();
	}

}



