import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { SkillModalComponent } from '../../modals/skill-modal/skill-modal.component';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';
import { ProjectsService } from '../../../Services/Projects/projects.service';

/* Interfaces */
import { needStatus2 } from '../../../Interfaces/Constants/status-constant';

/* Animations */
import { background } from '../../../Animations/animations';
import { newDialog } from '../../../Animations/slides3.animation';

/* Librairies */
import * as _ from 'underscore'

export interface ConfirmModel {
	project_id 	: number,
	opening 	: Object
}

@Component({
	selector: 'app-need-modal',
	templateUrl: './need-modal.component.html',
	styleUrls: ['./need-modal.component.scss'],
	animations: [newDialog, background],
	providers: [SkillsService, ProjectsService]
})
export class NeedModalComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	state 				: string = 'outUp'
	statutes 		: Object[] = needStatus2
	skills 			: Object[]
	selectedSkills 	: Object[] = []
	project_id 		: number
	opening 		: Object
	skillName 		: string
	currentStatus 	: string
	description 	: string
	hirePosition 	: string

	constructor(dialogService: DialogService,
				private SkillsService: SkillsService,
				private ProjectsService: ProjectsService) {
		super(dialogService)
	}

	ngOnInit() {
		this.currentStatus = 'for help'
		this.initServices();
		this.initOpening();
	}

	initOpening() {
		if (this.opening) {
			this.currentStatus 	= this.opening['status'];
			this.hirePosition 	= this.opening['position'];
			this.description 	= this.opening['description'];
			this.selectedSkills = this.opening['tags'].split(',');
		}
	}

	initServices() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills = res.skills
			this.state = 'initial'
		})
	}

	// selectSkill(name: string) {
	// 	if (this.selectedSkills.indexOf(name) < 0 && this.selectedSkills.length < 5) {
	// 		this.selectedSkills.push(name);
	// 		this.skillName = null
	// 	}
	// }

	selectStatus(name: string) {
		// if (name === 'Freelance' || name === 'Fundings') return
		this.currentStatus = name
	}

	removeSkill(name: string) {
		this.selectedSkills = _.without(this.selectedSkills, name);
	}

	processNeed() {
		const body = {
			status 		: this.currentStatus,
			description : this.description,
			position 	: this.hirePosition || '',
			tags 		: this.selectedSkills,
		}
		if (this.opening)
			this.updateProjectNeed(body);
		else
			this.createProjectNeed(body);
	}

	createProjectNeed(body) {
		this.ProjectsService.createProjectOpening(this.project_id, body).subscribe( res => {
			if (res.id) {
				body['id'] 		= res.id
				body['tags'] 	= this.selectedSkills.toString();
				this.hideSuccessModal(body);
			}
		})
	}

	updateProjectNeed(body) {
		this.ProjectsService.updateProjectOpening(this.opening['id'], body).subscribe( res => {
			if (res.success) {
				body['id'] 		= this.opening['id'];
				body['tags'] 	= this.selectedSkills.toString();
				this.hideSuccessModal(body);
			}
		});
	}

	/* SHOW MODAL */
	showSkillModal() {
		setTimeout(() => {
			this.state = 'outUp'
		}, 300)
		let disposable 	= this.dialogService.addDialog(SkillModalComponent, {
			selectedSkills : this.selectedSkills
		}).subscribe((skills: Array<string>) => {
			this.state 			= 'initial'
			this.selectedSkills = skills;
		});
	}

	hideModal() {
		this.state = 'outUp'
		this.close();
	}

	hideSuccessModal(object) {
		this.state = 'outUp'
		this.result = object
		this.close();
	}
}





