import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ExperiencesService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	getUserExperiences(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + id + '/experiences'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	addExperience(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + id + '/experiences'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updateExperience(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + id + '/experiences'

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	deleteExperience(id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url 	= 'http://localhost:3000/api/users/' + id + '/experiences'

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



