import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { ProfilesService } from '../../../../Services/Profiles/profiles.service';

/* Animations */
import { dialog, background } from '../../../../Animations/animations';

export interface ConfirmModel {
	profile_id 		: number
	profile_about 	: string
	profile_status 	: string
}

@Component({
	selector: 'app-profile-about',
	templateUrl: './profile-about.component.html',
	styleUrls: ['./profile-about.component.scss'],
	animations: [dialog, background],
	providers: [ProfilesService]
})
export class ProfileAboutComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	profile_id 		: number
	profile_about 	: string
	profile_status 	: string
	buttonText 		: string = 'Save'

	constructor(dialogService: DialogService,
				private ProfilesService: ProfilesService) {
		super(dialogService)
	}

	ngOnInit() {
	}

	selectStatus(status: string) {
		this.profile_status = status
	}

	saveAbout() {
		const body = {
			about		: this.profile_status,
			description	: this.replaceNewline(this.profile_about)
		}
		this.ProfilesService.updateProfile(this.profile_id, body).subscribe( res => {
			console.log(res);
			if (res.success)
				this.hideModalOnSuccess();
		});
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	hideModalOnSuccess() {
		this.buttonText = 'Saved'
		setTimeout( () => {
			this.result = { status: this.profile_status, description: this.profile_about };
			this.close();
		}, 1500)
	}

	hideModal() {
		this.close();
	}
}



