import { Component, OnInit, Inject } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';
/* Services */
/* Interfaces */
/* Libraries */
import * as _ from 'underscore';
/* Animations */
import { mobileDialog, background } from '../../../../Animations/animations';

export interface ConfirmModel {
	skills: Object
}

@Component({
	selector:    'app-skill-mobile-modal',
	templateUrl: './skill-mobile-modal.component.html',
	styleUrls:   ['./skill-mobile-modal.component.scss'],
	animations:  [mobileDialog, background]
})
export class SkillMobileModalComponent extends DialogComponent<ConfirmModel, Array<any>> implements OnInit {

	skills 			: Object
	selectedSkills 	: Object[] = []
	searchSkill 	: string
	state 			: string = 'up'
	message 		: string
	showModal 		: boolean = false

	constructor(dialogService: DialogService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.addPrevenScroll();
	}

	ngOnInit() {
		if (this.skills) {
			this.showModal = true
			if (this.state == 'up') {
				setTimeout(() => this.state = 'down')
			}
		}
	}

	selectSkill(name) {
		if (this.selectedSkills.indexOf(name) < 0 && this.selectedSkills.length < 5) {
			this.selectedSkills.push(name);
			// this.searchSkill = null;
		}
	}

	removeSkill(name: string) {
		this.selectedSkills = _.without(this.selectedSkills, name);
	}

	hideModalSuccess() {
		this.state = 'up'
		this.removePreventScroll();
		setTimeout(() => {
			this.result = this.selectedSkills
			this.close();
		}, 100);
	}

	hideModal() {
		this.state = 'up'
		this.removePreventScroll();
		setTimeout(() => {
			this.close();
		}, 300);
	}

	/* DOM PREVENT SCROLL */
	addPrevenScroll() {
		this.document.body.classList.add('prevent-scroll');
	}

	removePreventScroll() {
		this.document.body.classList.remove('prevent-scroll');
	}
}



