import { Component, OnInit, Inject, trigger, state, style, transition, animate, keyframes } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';
import { DOCUMENT } from '@angular/platform-browser';

/* Services */
import { SharedService } from '../../../Services/shared.service';
import { AccountService } from '../../../Services/account.service';
import { AuthenticationService } from '../../../Services/Authentication/auth-index';

/* Animations */
import { dialog, background } from '../../../Animations/animations';

@Component({
	selector: 'app-signup-modal',
	templateUrl: './signup-modal.component.html',
	styleUrls: [
		'./signup-modal.component.scss',
		'../../../../public/styles/login-signup-modal.scss'
	],
	animations: [dialog, background],
	providers: [AccountService, AuthenticationService]
})
export class SignupModalComponent implements OnInit {

	form 		: any = {}
	fn_err 		: boolean = false
	ln_err 		: boolean = false
	email_err 	: boolean = false
	pw_err 		: boolean = false
	eat 		: boolean = false
	visible 	: boolean = true


	validateForm(form) {
		this.fn_err = this.ln_err = this.email_err = this.pw_err = true

		if (form.first_name)
			this.fn_err = false
		if (form.last_name)
			this.ln_err = false
		if (form.email) {
			const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			this.email_err = !reg.test(form.email)
		}
		if (form.password)
			this.pw_err = form.password.length >= 8 ? false : true

		if (!this.fn_err && !this.ln_err && !this.email_err && !this.pw_err)
			return true
		else
			return false
	}

	constructor(private SharedService: SharedService,
				private AccountService: AccountService,
				private AuthenticationService: AuthenticationService,
				private route: ActivatedRoute,
				private router: Router,
				@Inject(DOCUMENT) private document: any) {
		if ((window.screen.width) < 736) {
			this.addPrevenScroll();
		}
	}

	ngOnInit() {
		this.form = {
			first_name	: '',
			last_name 	: '',
			email 		: '',
			password 	: ''
		}
	}

	switchL() {
		setTimeout( () => this.SharedService.setSwitch('Signup'), 300);
		this.visible = false
	}

	closeSignUpModal() {
		if ((window.screen.width) < 736) {
			this.removePreventScroll();
		}
		setTimeout( () => this.SharedService.setSignUpStatus(false), 300);
		this.visible = false
	}

	signUp() {
		if (this.validateForm(this.form)) {
			let body = { account: this.form };
			let login = { email: this.form.email, password: this.form.password };

			this.AccountService.createProfile(body).subscribe( res => {
				if (res.success) {
					this.closeSignUpModal();
					this.AuthenticationService.localLogIn(login).subscribe( res => {
						this.router.navigate(['/signup'], { queryParams: { id: true } });
			        });
				}
			}, error => {
				if (JSON.parse(error._body).error === 'Email already taken')
					this.eat = true;
			});
		}
	}

	/* DOM PREVENT SCROLL */
	addPrevenScroll() {
		this.document.body.classList.add('prevent-scroll');
	}

	removePreventScroll() {
		this.document.body.classList.remove('prevent-scroll');
	}
}



