export class TokenError extends Error {
	name = "TokenError";

	constructor(message: string) {
		super(message);
	}
}

export interface Token {

	auth: {
		token: string,
	};
	ttl: number;
	user: {
		id 			: number;
		email 		: string;
		moderator	: boolean

	};

  // public get authorization(): string {
  //     return this.auth.token;
  // }

  // public get session(): Object {
  //     return this.user || null;
  // }
}



