import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
// import { mergeMap } from 'rxjs/operators';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ProfilesService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	// getLocation(ip) : Observable<any> {
	// 	const url = 'https://freegeoip.net/json/' + ip

	// 	return this.http.get(url)
	// 		.map((res: Response) => res.json())
	// 		.catch((error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	// };

	// getIP() : Observable<any> {
	// 	const url = 'https://ipinfo.io'

	// 	return this.http.get(url)
	// 		.mergeMap((res: Response) => this.getLocation(res.json().ip))
	// 		.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	// }

	getIdByUsername(username: string) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + username

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getProfile(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getProfiles() : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	searchProfiles(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/search'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updateProfile(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))))
	}

	followProfile(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/profiles/' + id + '/follow'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))))
	}

}



