import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { InterestsService } from '../../../../Services/Interests/interests.service';
import { TokenService } from '../../../../Services/Authentication/token.service';

/* Animations */
import { dialog, background } from '../../../../Animations/animations';

export interface ConfirmModel {
	user_interests 	: Object
}

@Component({
	selector: 'app-profile-interest',
	templateUrl: './profile-interest.component.html',
	styleUrls: [
		'./profile-interest.component.scss',
	],
	animations: [dialog, background],
	providers: [
		InterestsService,
	]
})
export class ProfileInterestComponent extends DialogComponent<ConfirmModel, boolean> implements OnInit {

	interests 		: Object[] = []
	user_interests	: Object[]
	showDisplay 	: boolean = false
	showLoader 		: boolean = true
	user 			: any
	buttonText 		: string

	constructor(dialogService: DialogService,
				private InterestsService: InterestsService,
				private TokenService: TokenService) {
		super(dialogService)
	}

	ngOnInit() {
		this.getInterests();
		this.buttonText = 'Save'

		if (this.TokenService.getToken())
			this.user = this.TokenService.getToken().user
	}

	// confirm() {
	// 	// we set dialog result as true on click on confirm button,
	// 	// then we can get dialog result from caller code
	// 	this.result = true;
	// 	this.close();
	// }

	getInterests() {
		this.InterestsService.getInterests().subscribe( res => {
			this.interests = res.interests
			this.showLoader = false
			this.showDisplay = true
		});
	}

	addRemoveInterest(interest) {
		if (this.checkInterest(interest)) {
			this.InterestsService.addUserInterests(this.user.id, { name: interest }).subscribe( res => {
				if (res.interests)
					this.user_interests.push(interest);
			});
		} else {
			this.removeInterest(interest);
		}
	}

	removeInterest(interest) {
		this.InterestsService.deleteUserInterests(this.user.id, { name: interest }).subscribe( res => {
			if (res.interests)
				this.user_interests.splice(this.user_interests.indexOf(interest), 1)
		});
	}

	// addInterest(interest) {
	// 	if (this.checkInterest(interest)) {
	// 		this.InterestsService.addUserInterests(this.user.id, { name: interest }).subscribe( res => {
	// 			if (res.interests)
	// 				this.user_interests.push(interest);
	// 		});
	// 	}
	// }

	// removeInterest(index, interest) {
	// 	this.InterestsService.deleteUserInterests(this.user.id, { name: interest }).subscribe( res => {
	// 		if (res.interests)
	// 			this.user_interests.splice(index, 1)
	// 	});
	// }

	checkInterest(interest) {
		if (this.user_interests.indexOf(interest) >= 0)
			return false
		else
			return true
	}

	hideModal() {
		this.showDisplay = false
		this.close();
	}

	save() {
		this.buttonText = 'Saved'
		setTimeout( () => {
			this.showDisplay = false
			this.close();
		}, 1500)
	}

}




