import { trigger, state, animate, style, transition, keyframes } from '@angular/core';

/* Slide down/up dialog */
export const newDialog = trigger('dialog', [
		state('initial', style({
			opacity: 1, top: '50%'
		})),
    	state('outUp', style({
    		opacity: 0, top: '40%'
    	})),
    	transition('initial => outUp', animate('150ms')),
	    transition('outUp => initial', animate('150ms')),
]);



