import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { MessageModalComponent } from '../message-modal/message-modal.component';
import { GoogleContactModalComponent } from '../../modals/google-contact-modal/google-contact-modal.component';

/* Services */
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { AccountService } from '../../../Services/account.service';

/* Animations */
import { dialog, background } from '../../../Animations/animations';

export interface ConfirmModel {
	called_id 	: number,
	project_id 	: number
}

@Component({
	selector 	: 'app-search-modal-user',
	templateUrl	: './search-modal-user.component.html',
	styleUrls	: ['./search-modal-user.component.scss'],
	animations	: [dialog, background],
	providers 	: [ProfilesService, ProjectsService, TokenService, AccountService]
})
export class SearchModalUserComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	titleText 	: string
	profiles 	: Object[] = []
	called_id 	: number
	project_id 	: number
	showDisplay : boolean = false

	constructor(dialogService: DialogService,
				private ProfilesService: ProfilesService,
				private ProjectsService: ProjectsService,
				private TokenService: TokenService,
				private AccountService: AccountService) {
		super(dialogService)
	}

	ngOnInit() {
		if (this.called_id === 1)
			this.titleText = 'Search users'
		else
			this.titleText = 'Add members to your team'
		this.getProfiles();
	}

	getProfiles() {
		this.ProfilesService.getProfiles().subscribe( res => {
			this.profiles 		= res.profiles;
			this.showDisplay 	= true;
		});
	}

	selectUser(profile) {
		if (this.called_id === 1) {
			this.checkUserRank(profile);
			// setTimeout( () => {
			// 	this.showMessageModal(profile);
			// }, 500);
		} else {
			this.inviteTeammate(profile);
		}
	}

	checkUserRank(profile) {
		const my_id = this.TokenService.getToken().user.id
	  		// this.AccountService.getSocialInvite(my_id).subscribe( check => {
				this.ProfilesService.getProfile(my_id).subscribe( res => {
					// if (res.profile.rank <= 2500) {
						this.result = profile
						this.hideModal();
					// } else {
					// 	this.hideModal();
					// 	let disposable 	= this.dialogService.addDialog(GoogleContactModalComponent, {
			  //           	profile 	: profile,
			  //           	index 		: 1
				 //    	});
					// }
				});
			// });
	}

	inviteTeammate(profile) {
		this.ProjectsService.inviteToProject(this.project_id, { id: profile.uid }).subscribe( res => {
			if (res.success)
				this.hideModalSuccess();
		});
	}

	hideModal() {
		this.close();
	}

	hideModalSuccess() {
		this.result = true
		this.close();
	}

	/* SHOW MODAL */
	// showMessageModal(profile: Object) {
 //        let disposable 	= this.dialogService.addDialog(MessageModalComponent, {
 //            profile : profile
	//     }).subscribe( success => {
	//     	console.log(success);
	//     });
	// }
}





