import { Component, OnInit, Inject } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';
/* Services */
/* Interfaces */
/* Animations */
import { mobileDialog } from '../../../../Animations/animations';

export interface ConfirmModel {
	noVariable: string
}

@Component({
	selector:    'app-location-mobile-modal',
	templateUrl: './location-mobile-modal.component.html',
	styleUrls:   ['./location-mobile-modal.component.scss'],
	animations:  [mobileDialog]
})
export class LocationMobileModalComponent extends DialogComponent<ConfirmModel, string> implements OnInit {

	noVariable: string
	state: string = 'up'
	message: string
	showModal: boolean = false

	constructor(dialogService: DialogService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.hideBody();
	}

	ngOnInit() {
		if (this.state == 'up')
			setTimeout(() => this.state = 'down')
	}

	getAddress(place: Object) {
		const address = place['formatted_address'].replace('USA', 'United States');
		this.hideModalSuccess(address);
	}

	hideModalSuccess(location: string) {
		this.state = 'up'
		this.showBody();
		setTimeout(() => {
			this.result = location
			this.close();
		}, 300);
	}

	hideModal() {
		this.state = 'up'
		this.showBody();
		setTimeout(() => {
			this.close();
		}, 300);
	}

	/* DOM HIDE BODY */
	hideBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'none'
		this.document.body.querySelector('#w-app-body').style.display = 'none'
	}

	showBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'block'
		this.document.body.querySelector('#w-app-body').style.display = 'block'
	}
}



