import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { TokenService } from '../../../Services/Authentication/token.service';
import { SocialService } from '../../../Services/Social/social.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';

/* Animations */
import { dialog, background, inOutText } from '../../../Animations/animations';

/* Librairies */
import { LinkedInService } from 'angular-linkedin-sdk';

declare let gapi: any;
// declare let IN 	: any

export interface ConfirmModel {
	profile 	: Object
	index 		: number
}

export const text = [
	'🤘 Connecting your account',
	'...nearly done 🙈',
	"You're all set 🎉"
]

@Component({
	selector: 'app-google-contact-modal',
	templateUrl: './google-contact-modal.component.html',
	styleUrls: [
		'./google-contact-modal.component.scss'
	],
	animations: [dialog, background],
	providers: [SocialService, TokenService, ProfilesService]
})
export class GoogleContactModalComponent extends DialogComponent<ConfirmModel, boolean> implements OnInit {

	profile			: Object
	index 			: number
	waitText		: string
	my_rank 		: number
	barValue 		: number
	loadingGoogle	: boolean = false
	loadingDone 	: boolean = false
	scrapProgress 	: boolean = false
	timer 			: any

	isUserAuthenticated : any

	private basicProfileFields = ['id', 'first-name', 'last-name', 'public-profile-url', 'picture-url', 'picture-urls::(original)'];


	constructor(dialogService: DialogService,
				private TokenService: TokenService,
				private SocialService: SocialService,
				private ProfilesService: ProfilesService,
				private _linkedInService: LinkedInService,
				private chRef: ChangeDetectorRef) {
		super(dialogService)
	}

	ngOnInit() {
		this.initToken();
		if (this.index === 2) {
			this.waitText 		= text[0];
			// this._linkedInService.isUserAuthenticated$.subscribe({
			//     next: (state) => {
			//       this.isUserAuthenticated = state;
			//       if (this.isUserAuthenticated) {
			//       	this.subscribeToLogout();
			//       } else {
			//       	this.subscribeToLogin();
			//       }
			//     }
			//   });
			// setTimeout( () => {
			// 	this.connectLinkedInApi();
			// }, 500)
		} else if (this.index === 3) {
			this.waitText 		= text[0];
			setTimeout( () => {
				this.googleConnect();
			}, 500)
		}
	}

	initToken() {
		if (this.TokenService.getToken())
			this.initProfile(this.TokenService.getToken().user.id);
	}

	initProfile(id) {
		this.ProfilesService.getProfile(id).subscribe( res => {
			this.my_rank = res.profile.rank
		});
	}

	// SOCIAL //

	/** GOOGLE **/
	getContactListFromGoogle() {
		var config = {
			'client_id': '1000804181890-fdco880748jpkogh3s2q7s7dsdbq898s.apps.googleusercontent.com',
			'scope': 'https://www.google.com/m8/feeds',
			'authuser': -1
		};
		gapi.auth.authorize(config, () => {
			this.fetch(gapi.auth.getToken());
		});
	}

	fetch(token) {
		const body 			= { token : token.access_token };
		this.loadingDone 	= false
		this.loadingGoogle 	= true
		this.chRef.detectChanges();

		this.SocialService.connectGoogle(body).subscribe( res => {
			if (res) {
				setTimeout( () => {
					this.waitText = text[1];
				}, 3000);
				setTimeout( () => {
					this.loadingDone 	= true
					this.waitText 		= text[2];
					this.initToken();
					this.hideModalSuccess();
				}, 4000);
			}
		})
	}

	googleConnect() {
		this.waitText 		= text[0];
		this.getContactListFromGoogle();
	}

	// test
	subscribeToisInitialized(){
	    this._linkedInService.isInitialized$.subscribe({
	    next: (state) => {
	      // state will always return true when API finishes loading
	    },
	    complete: () => {
	      // Completed
	    }
	  });
	}

	subscribeToLogin(){
  	this._linkedInService.login().subscribe({
	    next: (state) => {
	    	console.log(state);
	      // state will always return true when login completed
	    },
	    complete: () => {
	      // Completed
	    }
	  });
	}

	subscribeToLogout(){
	  this._linkedInService.logout().subscribe({
	    next: () => {
	    	console.log('hello');
	      // does not emit a value
	    },
	    complete: () => {
	      // Completed
	    }
	  });
	}


	/** LinkedIn **/
	// connectLinkedInApi() {
	// 	console.log(IN.User.isAuthorized());
	// 	if (IN.User.isAuthorized()) {
	// 		// console.log(IN.User.isAuthorized());
	// 		try {
	// 			IN.User.logout( () => {
	// 				IN.User.authorize( () => this.retrieveLinkedInBasic());
	// 			});
	// 		} catch(err) {
	// 			console.log(err);
	// 		}
	// 	} else {
	// 		IN.User.authorize( () => this.retrieveLinkedInBasic());
	// 	}
	// }

	// retrieveLinkedInBasic() {
	// 	IN.API.Profile('me').fields(this.basicProfileFields)
	// 	.result( profiles => { this.linkedInService(profiles) })
	// 	.error( (err) => {
	//     	alert(err);
	//     });;
	// };

	// linkedInService(profiles) {

	// 	this.initInterval();
	// 	const body 			= {
	// 		profile: profiles.values[0].publicProfileUrl,
	// 		picture: profiles.values[0].pictureUrls.values[0]
	// 	};

	// 	setTimeout( () => {
	// 		this.waitText 	= text[1];
	// 	}, 6000);

	// 	this.SocialService.connectLinkedIn(body).subscribe( res => {
	// 		if (res.success) {
	// 			this.stopInterval();
	// 			setTimeout( () => {
	// 				this.scrapProgress 	= false
	// 				this.loadingDone 	= true
	// 				this.waitText 		= text[2];
	// 				this.result  		= true
	// 				setTimeout( () => {
	// 					this.close();
	// 				}, 2000)
	// 			}, 1000)
	// 		}
	// 	}, error => {
	// 		console.log(error);
	// 	})
	// }

	initInterval() {
		let count 			= 0
		this.scrapProgress 	= true
		this.barValue 		= 0
		this.timer 			= setInterval( () => {
			if (count > 16)
				this.stopInterval();
			else {
				this.barValue += 5
				count++
			}
		}, 1100);
	}

	stopInterval() {
		this.barValue = 100
		setTimeout( () => {
			clearInterval(this.timer);
		}, 1000)
	}

	hideModalSuccess() {
		if (!this.waitText || this.loadingDone) {
	 		setTimeout( () => {
				this.result = true
				this.close();
			}, 1000);
	 	}
	}

	hideModal() {
		if (!this.waitText || !this.loadingDone)
			this.close();
	}
}



