import { Injectable } from '@angular/core';
import { SocketService } from './Socket/socket.service';
import { Observable } from 'rxjs';

export class Notification {
  id: number;
  when: number;
  type: string;
  message: string;
  read: boolean = false;
  from: {
    id: number;
    picture?: string;
    full_name: string;
    username: string;
  };
  data?: any;
  route: string[];
}

export class Activity {
  when: number;
  type: string;
  message: string;
  from: {
    id: number;
    picture?: string;
    full_name: string;
    username: string;
  };
  to: {
    what: string;
  };
  route: string[];
  params: string[];
}

@Injectable()
export class NotificationService {
  private _eventlookup: Object = {
    'user_follow':    'is now following you',
    'project_update': 'just updated',
    'project_up':     'just upvoted',
    'profile_view':   'viewed your profile'
  };

  private _subjectLookup: Object = {
    'project_update': (data) => data.title,
    'project_up':     (data) => data.title
  };

  private _routeLookup: Object = {
    'user_follow': (data) => ['', data.from.username],
    'project_update': (data) => ['/project', data.public_id, data.title],
    'project_up': (data) => ['/project', data.public_id, data.title],
    'profile_view': (data) => ['', data.username]
  };

  constructor(private socket: SocketService) {}

  private _buildMessage(notification: any): string {
    let message: string;
    message = this._eventlookup[notification.type];
    if (this._subjectLookup.hasOwnProperty(notification.type))
      return `${message} ${this._subjectLookup[notification.type](notification.data)}`;
    return message
  }

  private _build(notification: any) {
    let n = notification as Notification;
    n.message = this._buildMessage(notification);
    n.route = this._routeLookup[notification.type](notification.data);
    return n;
  }

  data(): Observable<Notification[]> {
    return this.socket.notification() //tkt la magie
      .map(notifications => notifications.map(n => this._build(n)))

  }

  dataStream(): Observable<Notification> {
    return this.socket.fetch('server::notification::partials')
      .filter(notification => this._eventlookup.hasOwnProperty(notification.type))
      .map(notification => this._build(notification))
  }

  recentActivities(): Observable<Activity[]> {
    return this.socket.recentActivities()
      .map(activities => activities.map(activity => activity as Activity))
  }

  markAsRead(id?: number): void {
    this.socket.notificationMarkAsRead(id);
  }
}



