import { Injectable } from '@angular/core';

declare const window: any;

@Injectable()
export class PrerenderService {

	constructor() { }

	readytoRender(delay?: number): void {
		const waitFor: number = delay || 0;
		setTimeout(function () {
			window.prerenderReady = true;
		}, waitFor);
	}
}


