import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
	name : 'searchPipe',
})
export class SearchPipe implements PipeTransform {
	public transform(value, key: string, term: string, key2: string) {

		return value.filter((item) => {
			if (!term) {
				return item;
			} else if (key === 'noKey') {
				let regExp = new RegExp('\\b' + term, 'gi');
				return regExp.test(item);
			} else if (!key2 && item.hasOwnProperty(key)) {
				if (term) {
					let regExp = new RegExp('\\b' + term, 'gi');
					return regExp.test(item[key]);
				} else {
					return true
				}
			} else if (key2) {
				if (term) {
					let regExp = new RegExp('\\b' + term, 'gi');
					return regExp.test(item[key][key2]);
				} else
					return true
			} else {
				return false
			}
		});
	}
}

