import { Injectable } from '@angular/core';
import { Router, CanActivate } from '@angular/router';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';
/* Services */
/* Interfaces */
import { TokenService, Token } from './auth-index';
import { AuthHttp } from 'ng2-bearer';


@Injectable()
export class AuthenticationService implements CanActivate {

    constructor(private http: Http,
                private authHttp: AuthHttp,
                private tokenService: TokenService,
                private Router: Router) { }

    canActivate() {
        if (localStorage.getItem('auth')) {
            // logged in so return true
            return true;
        }
        // not logged in so redirect to login page
        this.Router.navigate(['/users/login']);
        return false;
    }

    checkSession() : Observable<any> {
        const url = 'http://localhost:3000/api/auth/session/verify'

        return this.authHttp.get(url)
            .map( (res:Response) => res.json())
            .catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
    }


    localLogIn(body: Object) {
        const url = 'http://localhost:3000/api/auth/local'

        return this.http.post(url, body)
            .map( response => {
                    let auth: Token = response.json() as Token
                    this.tokenService.setToken(auth);
                }
            )
            .catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))) );
    }

    logout() {
        const url = 'http://localhost:3000/api/auth/logout'

        return this.authHttp.post(url, {});
    }
}



