import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { CookieService } from 'ngx-cookie';
/* Interfaces */
import { Token } from './auth-index';

@Injectable()
export class TokenService {
	private _token: BehaviorSubject<Token>;

	constructor(private cookies: CookieService) {
		this._token = <BehaviorSubject<Token>> new BehaviorSubject(this._update());
	}

	private _update(): Token {
		if (this.cookies.getObject('auth')) {
			let token = this.cookies.getObject('auth') as Token;
			localStorage.setItem('auth', JSON.stringify(token));
			localStorage.setItem('token', token.auth.token);
			this.cookies.remove('auth');
			return token;
		} else {
			return JSON.parse(localStorage.getItem('auth')) as Token;
		}
	}

	getToken(): Token {
		return this._token.getValue() || null;
	}

	getTokenStream(): Observable<Token> {
		return this._token.asObservable();
	}

	setToken(auth: Token) {
		this._token.next(auth);
		if (auth) {
			localStorage.setItem('auth', JSON.stringify(auth));
			localStorage.setItem('token', auth.auth.token);
		}
	}

	removeToken() {
		this._token.next(null);
		localStorage.removeItem('auth');
		localStorage.removeItem('token');
	}
}




