import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
/* Pipes */
import { CutPipe } from './cut.pipe';
import { PasswordMeterPipe } from './password-meter.pipe';
import { SanitizeHtmlPipe } from './sanitize-html.pipe';
import { SanitizeUrlPipe } from './sanitize-url.pipe';
import { SearchPipe } from './search.pipe';
import { SortSkillPipe } from './sort-skill.pipe';
import { RoundPipe } from './round.pipe';
import { DecimalPipe } from './decimal.pipe';

@NgModule({
	imports: [
		CommonModule
	],
	declarations: [
		CutPipe,
		PasswordMeterPipe,
		SanitizeHtmlPipe,
		SanitizeUrlPipe,
    	SearchPipe,
    	SortSkillPipe,
    	RoundPipe,
    	DecimalPipe
	],
	exports:[
		CutPipe,
		PasswordMeterPipe,
		SanitizeHtmlPipe,
		SanitizeUrlPipe,
		SearchPipe,
		SortSkillPipe,
		RoundPipe,
		DecimalPipe
	]
})
export class PipeModule { }



