import { Directive, ElementRef, Renderer, Input, ViewContainerRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Directive({
	selector: '[h_sub1]',
	host:{
		'(ngModelChange)' 	: 'onInputChange($event)',
		'(focus)'			: 'onFocus($event)'
	}
})
export class HeaderDirectiveSub1 {

	nativeElement 	: any
	isWhite 		: boolean = false
	firstEntrance 	: boolean = true

	constructor(private el: ElementRef, private renderer: Renderer, private router: Router) {
		this.nativeElement = el.nativeElement.parentNode.parentNode
		this.checkPathChangeHeader();
	}

	checkPathChangeHeader() {
		this.router.events.subscribe((event) => {
			if(event instanceof NavigationEnd) {
				if (event.url === '/users/login' || event.url === '/users/register' ||
					event.url.indexOf('/admin/panel') >= 0) {
					this.switchToWhiteHeader();
					this.isWhite = true;
				} else {
					this.isWhite = false;
					this.nativeElement.querySelector('#whalgo').style.display = 'flex'
					this.switchToDefaultHeader();
				}
			}
		});;
	}

	switchToWhiteHeader() {
		if (this.nativeElement.querySelector('#whalgo').style.display === 'flex')
			return ;

		const image1 = '/public/images/new-logo-g.svg'
		const image2 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/waves-icon-b_swhyaz.png'
		const image3 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/mailbox-icon-b_lbsllf.svg'
		const image4 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/arrow-down-icon-b_opi301.svg'

		this.nativeElement.querySelector('#whl1-i').src 					= image1
		this.nativeElement.querySelector('#whl1').className 				+= ' wh-link-black'
		this.nativeElement.querySelector('#whl2').className 				+= ' wh-button-black'
		this.nativeElement.parentNode.querySelector('#w-header').className 	+= ' header-white'
		if (!this.firstEntrance)
			this.nativeElement.querySelector('#whalgo').style.display 		= 'flex'
		else
			this.firstEntrance = false

		if (this.nativeElement.querySelector('#whl5-1-i')) {
			this.nativeElement.querySelector('#whl5-1-i').src 				= image2
			this.nativeElement.querySelector('#whl5-2-i').src 				= image3
			this.nativeElement.querySelector('#whl5-3-i').src 				= image4
			this.nativeElement.querySelector('#whl5-3').className 			+= ' log-picture-black'
		} else
			this.nativeElement.querySelector('#whl4').className 			+= ' wh-log-white'
	
	}

	switchToDefaultHeader() {

		if (this.nativeElement.querySelector('#whalgo').style.display !== 'flex' || this.isWhite) {
			this.nativeElement.querySelector('#whalgo').style.display 		= 'none'
			return ;
		}

		const image1 = '/public/images/new-logo.svg'
		const image2 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/waves-icon-w_wslyzh.png'
		const image3 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/mailbox-icon-w_sji3lw.png'
		const image4 = 'https://res.cloudinary.com/dqpkpmrgk/image/upload/v1457892593/arrow-down-icon-w_csniet.svg'

		this.nativeElement.querySelector('#whl1-i').src 					= image1
		this.nativeElement.querySelector('#whl1').className 				= 'wh-link pad-l-20'
		this.nativeElement.querySelector('#whl2').className 				= 'wh-button'
		this.nativeElement.parentNode.querySelector('#w-header').className 	= 'transition-200'
		this.nativeElement.querySelector('#whalgo').style.display 			= 'none'

		if (this.nativeElement.querySelector('#whl5-1-i')) {
			this.nativeElement.querySelector('#whl5-1-i').src 				= image2
			this.nativeElement.querySelector('#whl5-2-i').src 				= image3
			this.nativeElement.querySelector('#whl5-3-i').src 				= image4
			this.nativeElement.querySelector('#whl5-3').className 			= 'pad-x-15 log-picture cursor-pt'
		} else
			this.nativeElement.querySelector('#whl4').className 			+= 'wh-log pad-r-10'
	}

	onInputChange(event){
		if (event.length > 0) {
			this.switchToWhiteHeader();
		} else {
			this.switchToDefaultHeader();
		}
	}

	onFocus($event) {
		if (this.nativeElement.querySelector('#whl3-i').value.length) {
			this.switchToWhiteHeader();
		}
	}

}



