import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';

/* Components */
import { HeaderComponent } from './header.component';
import { HeaderChatComponent } from './header-chat/header-chat.component'
import { HeaderNotifComponent } from './header-notif/header-notif.component';

/* Services */
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';

/* Directive */
import { HeaderDirective } from '../../Directives/Header/header.directive';
import { HeaderDirectiveSub1 } from '../../Directives/Header/header-sub1.directive';
import { HeaderAlgoliaComponent } from './header-algolia/header-algolia.component';

/* Librairies */
import { MomentModule } from 'angular2-moment';

@NgModule({
	imports: [
		CommonModule,
		RouterModule,
		FormsModule,
		PipeModule,
		MomentModule
	],
	declarations: [
		HeaderComponent,
        HeaderChatComponent,
        HeaderNotifComponent,
        HeaderDirective,
        HeaderDirectiveSub1,
        HeaderAlgoliaComponent
	],
	exports:[
		HeaderComponent,
        HeaderChatComponent,
        HeaderNotifComponent
	],
	providers: [PicturesService, ProfilesService]
})
export class HeaderModule { }



