import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';

/* Components */
import { SearchMobileModalComponent } from '../modals/mobile-modals/search-mobile-modal/search-mobile-modal.component';

/* Services */
import { AuthenticationService, TokenService } from '../../Services/Authentication/auth-index';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { SharedService } from '../../Services/shared.service';
import { NotificationService } from '../../Services/notification.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { TrackingService } from '../../Services/Trackings/tracking.service';

/* Directives */
import { HeaderDirectiveSub1 } from '../../Directives/Header/header-sub1.directive';

/* Constant */
import { page_id_list, zone_id_list } from '../../Interfaces/Constants/zones.constant';

@Component({
	selector:      'app-header',
	templateUrl:   './header.component.html',
	styleUrls:     ['./header.component.scss'],
	encapsulation: ViewEncapsulation.None,
	providers:     [AuthenticationService, ProfilesService, NotificationService]
})
export class HeaderComponent {

	profile 	: any
	log 		: boolean = false
	notif 		: boolean = false
	chat 		: boolean = false
	mobile 		: boolean = false
	moderator 	: boolean = false
	reloadHeader: any
	auth 		: any
	show 		: boolean = false
	unread_n 	: number
	unread_m 	: number
	navmobile 	: boolean = false
	page_id_list : Object = page_id_list

	constructor(private AuthenticationService: AuthenticationService,
		private SharedService: SharedService,
		private TokenService: TokenService,
		private ProfilesService: ProfilesService,
		private router: Router,
		private location: Location,
		private dialogService: DialogService,
		private PicturesService: PicturesService,
		private TrackingService: TrackingService,
		private route: ActivatedRoute,
		@Inject(DOCUMENT) private document: any) {

		this.checkSession();
		this.getToken();
		this.reloadProfile();
		this.showHeader();
		this.getLocation();

		if ((window.innerWidth) < 736)
			this.mobile = true
	}

	showHeader() {
		setTimeout(() => {
			this.show = true;
		}, 1000);
	}

	checkSession() {
		if (this.TokenService.getToken()) {
			this.AuthenticationService.checkSession().subscribe(res => {
				return ;
			}, error => {
				this.logOut();
			});
		}
	}

	getToken() {
		this.TokenService.getTokenStream().subscribe(auth => {
			if (!auth) this.log = false;
			else {
				this.auth 		= auth
				this.moderator 	= auth['user']['moderator']
				this.getProfile();
			}
		});
	}

	getLocation() {
		// this.ProfilesService.getIP().subscribe(res => {
		// 	let city, state, country;

		// 	city 	= res.city + ', '
		// 	country = res.country_name;

		// 	if (res.region_code)
		// 		state = res.region_code + ', '
		// 	else
		// 		state = "' ', "

			localStorage.setItem('geoLocation', ("Paris," + " ," + "France"));
		// });
	}

	updateTracking(zone_id) {
		const token = this.TokenService.getToken();
		const url 	= this.router.url;
		let page_id;

		if (url.indexOf('/invite/s5fZnFqodHSDS8SOPHfWZxUf2ssFbEnP') >= 0)
			page_id = this.page_id_list['inv_bulk'];
		else if (url.indexOf('/invite/') >= 0 && url.indexOf('/invite/s5fZnFqodHSDS8SOPHfWZxUf2ssFbEnP') === -1)
			page_id = this.page_id_list['inv_user'];
		else if (url.indexOf('/welcome/') >= 0)
			page_id = this.page_id_list['inv_network'];
		else if (url.indexOf('/'))
			page_id = this.page_id_list['landing_page'];
		else if (url.indexOf('/welcome') >= 0)
			page_id = this.page_id_list['home_page'];
		else if (url.indexOf('/discover') >= 0)
			page_id = this.page_id_list['discover_page'];
		else if (url.indexOf('/meet') >= 0)
			page_id = this.page_id_list['meet_page'];
		else if (url.indexOf('/learn') >= 0)
			page_id = this.page_id_list['learn_page'];
		else
			page_id = 0;

		this.TrackingService.clickActivities({}, token, page_id, zone_id).subscribe(r => {
			if (zone_id === 1)
				this.router.navigate(['/']);
			else if (zone_id === 2)
				this.router.navigate(['/discover']);
			else if (zone_id === 3)
				this.router.navigate(['/meet']);
			else if (zone_id === 4)
				this.router.navigate(['/learn']);
		})
	}

	reloadProfile() {
		this.reloadHeader = this.SharedService.headerReload$.subscribe(res => {
			if (res)
				this.getProfile();
		});
	}

	getProfile() {
		this.ProfilesService.getProfile(this.auth.user.id).subscribe(res => {
			this.profile = res.profile;
			this.log = true;
			if (!this.profile.cover_picture)
				this.router.navigate(['/signup'], {queryParams: {id: true}});
		});
	}

	unreadCountNotif(event) {
		this.unread_n = event
	}

	unreadCountMessage(event) {
		this.unread_m = event
	}

	hideBubble(type) {
	}

	onResize(event) {
		this.mobile = event.target.innerWidth < 736;
	}

	activePost() {
		this.updateTracking(5);
		if (!this.location.path()) this.SharedService.postActiveSignal(true);
		else {
			this.router.navigate(['/']);
			setTimeout(() => {
				this.SharedService.postActiveSignal(true);
			}, 1000);
		}
	}

	signIn() {
		this.SharedService.setSignUpStatus(true);
	}

	logIn() {
		this.SharedService.setLoginStatus(true);
	}

	logOut() {
		this.AuthenticationService.logout().subscribe(res => {
			this.TokenService.removeToken();
			window.location.href = 'https://www.wittycircle.com'
		},
		err => {
			this.TokenService.removeToken();
			this.router.navigate(['/']);
		});
	}

	showNotif() {
		this.notif = !this.notif;
	}

	showChat() {
		this.chat = !this.chat;
	}

	/* TRANFORM */
	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	/* MOBILE */
	showNavMobile() {
		if (this.navmobile === false) {
			this.navmobile = true
			this.SharedService.setNavMobileStatus(true);
			if (this.log) {
				setTimeout( () => {
					this.document.body.classList.add('slide-body');
				}, 300)
			}
		} else {
			this.navmobile = false
			this.SharedService.setNavMobileStatus(false);
			this.document.body.classList.remove('slide-body');
			this.document.body.classList.remove('prevent-scroll');
		}
	}

	showSearchMobileModal() {
		let disposable = this.dialogService.addDialog(SearchMobileModalComponent, {
		})
	}
}



