import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { SharedService } from '../../../Services/shared.service';

/* Animations */
import { dialogPopover, background } from '../../../Animations/animations';

export interface ConfirmModel {
	project_id 	: number
}

@Component({
	selector: 'app-popover-login',
	templateUrl: './popover-login.component.html',
	styleUrls: [
		'./popover-login.component.scss',
		'../../../../public/styles/login-signup-modal.scss'
	],
	animations: [background, dialogPopover]
})
export class PopoverLoginComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	constructor(dialogService: DialogService,
				private SharedService: SharedService) {
		super(dialogService)
	}

	ngOnInit() {
	}

	switchToSignUp() {
		this.hideModal();
		setTimeout( () => {
			this.SharedService.setSignUpStatus(true);
		}, 500)
	}

	hideModal() {
		this.close();
	}
}



