import { Component, OnInit, Inject } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';
/* Services */
/* Interfaces */
/* Animations */
import { mobileDialog } from '../../../../Animations/animations';

export interface ConfirmModel {
	categories: Object
}

@Component({
	selector:    'app-category-mobile-modal',
	templateUrl: './category-mobile-modal.component.html',
	styleUrls:   ['./category-mobile-modal.component.scss'],
	animations:  [mobileDialog]
})
export class CategoryMobileModalComponent extends DialogComponent<ConfirmModel, string> implements OnInit {

	categories: Object
	state: string = 'up'
	message: string
	showDisplay: boolean = false

	constructor(dialogService: DialogService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.addPrevenScroll();
	}

	ngOnInit() {
		if (this.categories) {
			if (this.state == 'up') {
				setTimeout(() => this.state = 'down')
			}
		}
	}

	selectCategory(name: string) {
		this.hideModalSuccess(name);
	}

	hideModalSuccess(category: string) {
		this.state = 'up'
		this.removePreventScroll();
		setTimeout(() => {
			this.result = category
			this.close();
		}, 100);
	}

	hideModal() {
		this.state = 'up'
		this.removePreventScroll();
		setTimeout(() => {
			this.close();
		}, 300);
	}

	/* DOM PREVENT SCROLL */
	addPrevenScroll() {
		this.document.body.classList.add('prevent-scroll');
	}

	removePreventScroll() {
		this.document.body.classList.remove('prevent-scroll');
	}
}



