import { Component, OnInit, Inject } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';

/* Services */

/* Interfaces */

/* Animations */
import { mobileDialog } from '../../../../Animations/animations';

export interface ConfirmModel {
	statutes: Object
}

@Component({
	selector:    'app-status-mobile-modal',
	templateUrl: './status-mobile-modal.component.html',
	styleUrls:   ['./status-mobile-modal.component.scss'],
	animations:  [mobileDialog]
})
export class StatusMobileModalComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	statutes 	: Object
	state 		: string = 'up'
	message 	: string
	showModal 	: boolean = false

	constructor(dialogService: DialogService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.hideBody();
	}

	ngOnInit() {
		if (this.statutes) {
			this.showModal = true
			if (this.state == 'up') {
				setTimeout(() => this.state = 'down')
			}
		}
	}

	selectStatus(status: string, search: string) {
		this.hideModalSuccess({status: status, searchName: search});
	}

	hideModalSuccess(status) {
		this.state = 'up'
		this.showBody();
		setTimeout(() => {
			this.result = status
			this.close();
		}, 100);
	}

	hideModal() {
		this.state = 'up'
		this.showBody();
		setTimeout(() => {
			this.close();
		}, 300);
	}

	/* DOM HIDE BODY */
	hideBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'none'
		this.document.body.querySelector('#w-app-body').style.display = 'none'
	}

	showBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'block'
		this.document.body.querySelector('#w-app-body').style.display = 'block'
	}
}



