import { Injectable } from '@angular/core';
import { Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

/* Interfaces */
import { cover_pictures, profile_pictures } from '../../Interfaces/Constants/default-pictures-constant'

@Injectable()
export class PicturesService {

	private cover_pictures 		: Array<string> = cover_pictures
	private profile_pictures 	: Array<string> = profile_pictures

	constructor(private AuthHttp: AuthHttp) { }

	transformImage(url, width, height, crop) {
		if (url && url.indexOf('cloudinary') >= 0 && url.indexOf('z_0.9') < 0) {
			let tab, i, parameter, url_ret

			crop 		= crop || 'fill'
			width 		? width = "w_" + width + "," : ""
			height 		? height = "h_" + height + "," : ""

			tab 		= url.split('/');
			i 			= tab.indexOf('upload');
			parameter 	= width + height + "c_" + crop + ",g_face"
			tab.splice(i + 1, 0, parameter);
			url_ret 	= tab.join('/');

			return url_ret
		} else
			return url
	}

	getRandomPicture(index) {
		const rand 	= Math.floor((Math.random() * this.cover_pictures.length) + 1) - 1;
		if (!index) {
			const body 	= {
				cover_picture	: this.cover_pictures[rand],
				picture			: this.profile_pictures[rand]
			}

			return body
		} else {
			const body 	= {
				cover_picture	: this.cover_pictures[rand],
			}
			return body
		}
	}

	setDefaultProfilePicture(index: number, id: number) : Observable<any> {
		const url 	= 'http://localhost:3000/api/profiles/' + id

		return this.AuthHttp.put(url, this.getRandomPicture(index))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	uploadPicture(nature: string, type: string, pictureUrl: Object) : Observable<any> {
		const url 	= 'http://localhost:3000/api/upload/' + nature + '/' + type

		return this.AuthHttp.post(url, pictureUrl)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

}



