import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ProjectsService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }


	// Create Project
	createProject(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Get single project
	getProject(public_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + public_id

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Get all projects
	getProjects() : Observable<any> {
		const url = 'http://localhost:3000/api/projects'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error:any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Get User projects
	getUserProjects(user_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + user_id + '/projects'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error:any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	//Get project openings
	getProjectOpenings(project_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + project_id + '/openings'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Search project
	searchProject(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/search'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Get single project
	updateProject(project_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + project_id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Follow Project
	followProject(project_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + project_id + '/up'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Delete Project
	deleteProject(project_id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url = 'http://localhost:3000/api/projects/' + project_id

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	/* INVITE TEAMMATE TO PROJECT */
	getInviteToProject(project_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + project_id + '/invite'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	inviteToProject(project_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + project_id + '/invite'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	deleteInviteToProject(project_id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url = 'http://localhost:3000/api/projects/' + project_id + '/invite'

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	/* PROJECT OPENINGS */
	createProjectOpening(project_id: number, body: Object) : Observable<any> {
		console.log(project_id);
		const url = 'http://localhost:3000/api/projects/' + project_id + '/openings'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updateProjectOpening(opening_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/openings/' + opening_id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	deleteProjectOpening(opening_id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url = 'http://localhost:3000/api/openings/' + opening_id

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

}



