import { Directive, ElementRef } from '@angular/core';

@Directive({
	selector: '[cpDiv]',
	host: {
		'(document:click)' 	: 'onClick($event)',
	},
})
export class CreateProjectDirective {

	nativeElement : any
	target : any

	constructor(private el: ElementRef) {
		this.nativeElement = el.nativeElement
	}

	showCategories() {
		if (this.nativeElement.querySelector('#wcpbicb-box').style.display !== 'block')
			this.nativeElement.querySelector('#wcpbicb-box').style.display = 'block'
		else
			this.hideCategories();
	}

	hideCategories() {
		this.nativeElement.querySelector('#wcpbicb-box').style.display = 'none';
	}

	onClick(event) {
		if (this.nativeElement.querySelector('#wcpbicb-i')) {
			if (this.nativeElement.querySelector('#wcpbicb-i').contains(event.target)) {
				this.showCategories();
			} else
				this.hideCategories();
		}
	}
}


