import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
import { SocketService } from '../../../Services/Socket/socket.service';
import { TokenService } from '../../../Services/Authentication/token.service';

/* Interfaces */

/* Animations */
import { dialog, background } from '../../../Animations/animations';

export interface ConfirmModel {
	profile 	: Object,
	roomId	 	: number
}

@Component({
	selector: 'app-message-modal',
	templateUrl: './message-modal.component.html',
	styleUrls: ['./message-modal.component.scss'],
	animations: [dialog, background]
})
export class MessageModalComponent extends DialogComponent<ConfirmModel, boolean> implements OnInit {

	profile 	: Object
	roomId 		: number
	message 	: string
	buttonText 	: string = 'Send'
	showDisplay : boolean = false
	my_id 		: Object

	constructor(dialogService: DialogService,
				private SocketService: SocketService,
				private TokenService: TokenService) {
		super(dialogService)
	}

	ngOnInit() {
		if (this.profile) {
			this.showDisplay = true
			this.initToken();
		}
	}

	initToken() {
		if (this.TokenService.getToken() && this.TokenService.getToken().user)
			this.my_id = this.TokenService.getToken().user.id
	}

	sendMessage() {
		let body = {
			message : this.message,
			sender 	: this.my_id,
			members : [this.my_id, this.profile['uid']],
		}

		if (this.roomId)
			body['roomId'] = this.roomId
		this.SocketService.sendMessage(body);
		this.hideModalSuccess();
	}

	hideModalSuccess() {
		this.buttonText = 'Sent'
		setTimeout( () => {
			this.result = true
			this.close();
		}, 300);
	}

	hideModal() {
		if (this.message) {
			this.close();
		} else
			this.close();
	}
}



