import { Directive, ElementRef } from '@angular/core';

@Directive({
	selector: '[aboutDiv]',
	host: {
		'(document:click)' 	: 'onClick($event)',
	},
})
export class AboutModalDirective {

	nativeElement : any
	target : any

	constructor(private el: ElementRef) {
		this.nativeElement = el.nativeElement
	}

	rotateDown() {
		this.nativeElement.querySelector('#ssd-i').style.transform = 'rotate(0)'
	}

	rotateUp() {
		this.nativeElement.querySelector('#ssd-i').style.transform = 'rotate(180deg)'
	}

	showAbout() {
		if (this.nativeElement.querySelector('#ssdbox').style.display !== 'block') {
			this.nativeElement.querySelector('#ssdbox').style.display = 'block'
			this.rotateUp();
		} else
			this.hideAbout();
	}

	hideAbout() {
		this.rotateDown();
		this.nativeElement.querySelector('#ssdbox').style.display ='none'
	}

	onClick(event) {
		if (this.nativeElement.querySelector('#ssd').contains(event.target)) {
			this.showAbout();
		} else {
			this.hideAbout();
		}
	}
}


