import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { NotFoundComponent } from '../Components/not-found/not-found.component';
import { PrivacyComponent } from '../Components/privacy/privacy.component';
import { TermsComponent } from '../Components/terms/terms.component';

/* Services */
import { AuthenticationService } from '../Services/Authentication/authentication.service';

/* ROUTES */
const routes: Routes = [
	{ path: '', loadChildren: '../Components/landing/landing.module#LandingModule'},
	{ path: 'users', loadChildren: '../Components/authentications/authentications.module#AuthenticationsModule'},
	{ path: 'privacy', component: PrivacyComponent },
	{ path: 'terms', component: TermsComponent },
	{ path: 'welcome', loadChildren: '../Components/home/home.module#HomeModule' },
	{ path: 'meet', loadChildren: '../Components/meet/meet.module#MeetModule' },
	{ path: 'discover', loadChildren: '../Components/discover/discover.module#DiscoverModule' },
	{ path: 'learn', loadChildren: '../Components/learn/learn.module#LearnModule' },
	{ path: 'project/update', loadChildren: '../Components/create-projects/create-projects.module#CreateProjectsModule' },
	{ path: 'messages', canActivate: [AuthenticationService], loadChildren: '../Components/messaging/messaging.module#MessagingModule' },
	{ path: 'settings', loadChildren: '../Components/settings/settings.module#SettingsModule' },
	{ path: 'signup', loadChildren: '../Components/signup/signup.module#SignupModule' },
	{ path: 'network/:name', loadChildren: '../Components/networks/networks.module#NetworksModule' },
	{ path: 'project/:public_id/:title', loadChildren: '../Components/projects/projects.module#ProjectsModule' },
	{ path: 'validation', loadChildren: '../Components/validations/validations.module#ValidationModule' },
	{ path: 'invite', loadChildren: '../Components/invitations/invitations.module#InvitationsModule' },
	{ path: 'admin/panel', loadChildren: '../Components/backoffice/backoffice.module#BackofficeModule' },
	{ path: 'statistics', canActivate: [AuthenticationService], loadChildren: '../Components/statistics/statistics.module#StatisticsModule' },
	{ path: ':username', loadChildren: '../Components/profiles/profiles.module#ProfilesModule' },
	{ path: 'reset/password/:token', loadChildren: '../Components/reset-password/reset-password.module#ResetPasswordModule'},
	{ path: '**', component: NotFoundComponent }

];

export const APP_ROUTES: ModuleWithProviders = RouterModule.forRoot(routes);



