import { Component, OnInit, HostListener } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { MessageModalComponent } from '../message-modal/message-modal.component';
import { GoogleApiContactsComponent } from '../../modals/google-api-contacts/google-api-contacts.component';

/* Services */
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { AccountService } from '../../../Services/account.service';
import { InvitationService } from '../../../Services/Invitation/invitation.service';

/* Animations */
import { background2, bounceEaseRepeat } from '../../../Animations/animations';

export interface ConfirmModel {
	check : boolean
}

@Component({
	selector: 'app-invitation-social',
	templateUrl: './invitation-social.component.html',
	styleUrls: ['./invitation-social.component.scss'],
	animations: [background2, bounceEaseRepeat],
	providers: [InvitationService]
})
export class InvitationSocialComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	selectText 		: string
	emails2String 	: string
	selectAll 		: boolean = true
	firstStep		: boolean = true
	secondStep		: boolean = false
	display1 		: boolean = true
	display2 		: boolean = false
	display3 		: boolean = false
	loading 		: boolean = false
	sent 			: boolean = false
	sending 		: boolean = false
	lifeTime 		: boolean = false
	loadingEmails 	: boolean = false
	mailsSelected 	: number

	// mailsPercentage : number
	emails2Length 	: number = 0
	paginate 		: number = 10
	countFrom 		: number = 0
	countFrom2		: number = 0
	emails 			: Object[] = []
	// emails2			: Array<string> = []

	// Animation Statements
	_stateWrap 			: string = 'fadeOut'
	// _statePageButtons 	: string = 'fadeIn'
	_statePageEmail 	: string = 'fadeOut'
	_stateEase1 		: string = 'initial'
	_stateEase2			: string = 'initial'
	_stateEase3 		: string = 'initial'

	constructor(dialogService: DialogService,
			private InvitationService: InvitationService) {
		super(dialogService)
	}

	ngOnInit() {
		this._stateWrap 		= 'fadeIn'
		this._stateEase1 		= 'bounceIn'
		this._stateEase2 		= 'bounceInDelay1'
		this._stateEase3 		= 'bounceInDelay2'
		this.selectText 		= 'Unselect all'
	}

	inviteByEmail() {
		this.display2 			= true
		// this._stateEase3		= 'fadeOut'
		this._statePageEmail	= 'fadeIn'
		// setTimeout(() => {
			this.display1 			= this.display3 = this.sending = this.sent = false
		// }, 300)
	}

	showGoogleAuth() {
		this.display1 	= this.display2 = this.display3 = this.sending = this.sent = false
		this.loading 	= true

		this.showSocialModal();
	}

	showSocialModal() {
		let disposable 	= this.dialogService.addDialog(GoogleApiContactsComponent, {
        	profile 	: {},
        	index 		: 3
    	}).subscribe( res => {
    		this.emails 		= res['contacts']
    		this.mailsSelected 	= this.emails.length
    		this.loading 		= false
			this.display3 		= true
    	});
	}

	selectAllEmails() {
		this.countFrom 			= this.mailsSelected

		this.emails.forEach((e) => {
			if (!this.selectAll)
				e['select'] = true
			else
				e['select'] = false
		});
		this.selectText 		= this.selectAll ? 'Select all' : 'Unselect all'
		this.selectAll 			= this.selectAll ? false : true

		if (!this.selectAll) {
			this.mailsSelected 		= 0
		} else {
			this.mailsSelected 		= this.emails.length
		}
	}

	selectEmail(email) {
		const index 			= email[Object.keys(email)[0]]
		this.countFrom 			= this.mailsSelected

		if (!this.emails[index]['select']) {
			this.emails[index]['select'] = true
			this.mailsSelected 	+= 1
		} else {
			this.emails[index]['select'] = false
			this.mailsSelected -= 1
		}

		// this.mailsPercentage = this.mailsSelected * 12

		if (this.mailsSelected === this.emails.length) {
			this.selectAll 	= true
			this.selectText = 'Unselect all'
		} else {
			this.selectAll 	= false
			this.selectText = 'Select all'
		}
	}

	checkList(array) {
		this.emails2Length 	= 0
		this.countFrom2 	= this.emails2Length

		array.forEach((e) => {
			if (e.indexOf('@') >= 0)
				this.emails2Length++;
		});
	}

	parsingEmails() {
		const reg 		= /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
		let emails 		= this.emails2String.replace(/\s+/g, '').split(',');
		let length 		= emails.length
		let newMails 	= []

		for (let i = 0; i < length; i++) {
			if (reg.test(emails[i])) {
				newMails.push(emails[i]);
			}
		}

		return newMails
	}

	sendInvitation(index) {
		this.firstStep 	= this.display1 = this.display2 = this.display3 = this.sent = false
		this.secondStep = true
		this.sending 	= true

		let data : Array<string> = [];

		if (index === 1 && this.emails2String)
			data = this.parsingEmails();
		else {
			this.emails.forEach(e => {
				if (e['select'])
					data.push(e['email']);
			});
		}

		this.send(data);
	}

	send(emails) {
		this.InvitationService.sendInvitation({ mail: emails }).subscribe( res => {
			this.sendingProcess();
		});
	}

	sendingProcess() {
		setTimeout(() => {
			this.sending 	= false
			this.sent 		= true

			setTimeout(() => {
				this.hideModal();
			}, 2000)
		}, 2000);
	}

	onChange($event) {
		this.checkList($event.split(', '))
	}

	/* ON SCROLL EVENT */
	onContainerScroll() {
		let container = document.getElementById('iple');

		let number 		= container.scrollTop
		let number2 	= container.scrollHeight
		let number3 	= container.offsetHeight

		if (number === (number2 - number3)) {
			if (this.emails.length > this.paginate) {
				this.loadingEmails 		= true;
				setTimeout(() => {
					this.loadingEmails 	= false;
					this.paginate 		+= 5;
				}, 2000)
			}
		}
	}

	disableParentScroll($event) {
		let body = document.getElementsByTagName('body')[0];

		body.style.overflow = 'hidden';
	}

	activateParentScroll() {
		let body = document.getElementsByTagName('body')[0];

		body.style.overflow = 'auto';
	}

	hideModal() {
		this._stateWrap 		= 'fadeOut'
		this._statePageEmail 	= 'fadeOut'
		this._stateEase1 		= 'initial'
		this._stateEase2 		= 'initial'
		this._stateEase3 		= 'initial'

		setTimeout(() => {
			this.close();
		}, 100)
	}

	hideModalSuccess() {
		this.result = true
		this.hideModal();
	}
}



