import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sortSkill'
})
export class SortSkillPipe implements PipeTransform {

  transform(input) {
    if (input && input[0]) {
      const lengthText = input.length;
      let text = "",
        i = 0,
        n = 0,
        count = 0;

      if (!input[1]) {
        text = input[0];
        if (text.length > 20)
          text = text.substring(0, 16) + "...";
      } else {
        text = input[0];
        if (text.length > 14) {
          text = text.substring(0, 13) + "..., +" + (lengthText - 1).toString();
        } else
          text += ", +" + (lengthText - 1).toString();
      }
      return text;
    } else
      return 'anything'
  }
}



