import { Component, OnInit } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Services */
// import { TokenService } from '../../../../Services/Authentication/token.service';
import { SocialService } from '../../../../Services/Social/social.service';
// import { ProfilesService } from '../../../../Services/Profiles/profiles.service';

/* Animations */
import { dialog, background, inOutText } from '../../../../Animations/animations';

/* Librairies */
import { LinkedInService } from 'angular-linkedin-sdk';

export interface ConfirmModel {
	profile 	: Object
	index 		: number
}

export const text = [
	'🤘 Connecting your account',
	'...nearly done 🙈',
	"You're all set 🎉"
]

@Component({
	selector: 'app-linked-in-api',
	templateUrl: './linked-in-api.component.html',
	styleUrls: ['./linked-in-api.component.scss'],
	animations: [dialog, background],
	providers: [SocialService]
})
export class LinkedInApiComponent extends DialogComponent<ConfirmModel, boolean> implements OnInit {

	private basicProfileFields = ['id', 'first-name', 'last-name', 'public-profile-url', 'picture-url', 'picture-urls::(original)', 'positions'];

	index 			: number
	waitText		: string
	barValue 		: number
	loadingDone 	: boolean = false
	scrapProgress 	: boolean = false
	timer 			: any

	public isUserAuthenticatedEmittedValue = false;
	public isInitializedEmittedValue = false;
	public isUserAuthenticated;
	public isInitialized;
	public lastResponse;
	public apiKey;
	public anonymousToken;

	constructor(dialogService: DialogService,
				private SocialService: SocialService,
				private _linkedInService: LinkedInService) {
		super(dialogService)
	}

	ngOnInit() {
		this.waitText 				= text[0];
		this.isUserAuthenticated 	= this._linkedInService.isUserAuthenticated$;
		this.isInitialized 			= this._linkedInService.isInitialized$;

		// this._linkedInService.isUserAuthenticated$.subscribe({
		// 	next: (state) => {
		// 		console.log(state);
		// 		this.isUserAuthenticatedEmittedValue = true;
		// 	}
		// });

		this._linkedInService.isInitialized$.subscribe({
			next: (state) => {
				this.isInitializedEmittedValue = true;
				if (state) {
					setTimeout(() => {
						this.login()
					}, 500);
				}
			}
		});
	}

	login() {
		this._linkedInService.login().subscribe({
			next: (state) => {
				console.log(`Login result: ${state}`);
				if (state)
					this.linkedInFetchProfile();
			}
		});
	}

	refresh() {
		this._linkedInService.refresh().subscribe({
			next: (value) => {
				console.log(`Refresh result: ${value}`);
			}
		});
	}

	logout() {
		this._linkedInService.logout().subscribe({
			next: () => {
				console.log('Logout emitted.');
			},
			complete: () => {
				console.log('Logout completed.');
			}
		});
	}

	linkedInFetchProfile() {
		let url =`/people/~:(${this.basicProfileFields.join(',')})?format=json'`;
		this._linkedInService.raw(url)
		.asObservable()
		.subscribe({
			next: (data) => {
				// console.log(data);
				this.sendLinkedInData(data);
			},
			error: (err) => {
				console.log(err);
			},
			complete: () => {
				console.log('RAW API call completed');
				this.refresh();
			}
		});
	}

	sendLinkedInData(profile) {

		this.initInterval();
		const body 			= {
			profile: profile.publicProfileUrl,
			picture: profile.pictureUrls.values[0]
		};

		setTimeout( () => {
			this.waitText 	= text[1];
		}, 6000);

		this.SocialService.connectLinkedIn(body).subscribe( res => {
			if (res.success) {
				this.stopInterval();
				setTimeout( () => {
					this.scrapProgress 	= false
					this.loadingDone 	= true
					this.waitText 		= text[2];
					this.result  		= true
					setTimeout( () => {
						this.close();
					}, 2000)
				}, 1000)
			}
		}, error => {
			console.log(error);
		})
	}

	initInterval() {
		let count 			= 0
		this.scrapProgress 	= true
		this.barValue 		= 0
		this.timer 			= setInterval( () => {
			if (count > 16)
				this.stopInterval();
			else {
				this.barValue += 5
				count++
			}
		}, 1100);
	}

	stopInterval() {
		this.barValue = 100
		setTimeout( () => {
			clearInterval(this.timer);
		}, 1000)
	}

	hideModalSuccess() {
		if (!this.waitText || this.loadingDone) {
	 		setTimeout( () => {
				this.result = true
				this.close();
			}, 1000);
	 	}
	}

	hideModal() {
		if (!this.waitText || !this.loadingDone)
			this.close();
	}
}



