import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class AccountService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	createProfile(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/accounts/register'

		return this.http.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	accountValidation(token: string) : Observable<any> {
		const url = 'http://localhost:3000/api/accounts/activate/' + token

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	resetPassword(token: string, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/accounts/password-reset/' + token

		return this.http.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	passwordRecoveryEmail(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/accounts/password-reset'

		return this.http.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updateAccountInfo(body: Object) {
		const url = 'http://localhost:3000/api/accounts/informations'

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updatePassword(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/accounts/password'

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getUserInviteId(user_id: number) {
		const url = 'http://localhost:3000/api/users/' + user_id + '/invite_link'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getSocialInvite(user_id: number) {
		const url = 'http://localhost:3000/api/users/' + user_id + '/social_invite'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



