import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class SkillsService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	getSkills() : Observable<any> {
		const url = 'http://localhost:3000/api/skills'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getSkillCategories() : Observable<any> {
		const url = 'http://localhost:3000/api/skills/categories'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getSkillsByCategories(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/skills/categories'

		return this.http.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getUserSkills(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + id + '/skills'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	addUserSkills(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/users/' + id + '/skills'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	deleteUserSkills(id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url = 'http://localhost:3000/api/users/' + id + '/skills'

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



