import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class InvitationService {

	constructor(private AuthHttp: AuthHttp, private Http: Http) { }

	getUserByInviteId(invite_id: string) : Observable<any> {
		const url = 'http://localhost:3000/api/invite/' + invite_id

		return this.Http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	registerInvitation(invite_id: string, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/invite/' + invite_id

		return this.Http.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	sendInvitation(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/invite'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	sendGoogleAuthInvitation() : Observable<any> {
		const url = 'http://localhost:3000/api/invite/signup/google'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



