import { Component, OnInit, Inject, ElementRef, ViewChild, AfterViewChecked } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';

/* Services */
import { SocketService } from '../../../../Services/Socket/socket.service';
import { ProfilesService } from '../../../../Services/Profiles/profiles.service';
import { TokenService } from '../../../../Services/Authentication/token.service';

/* Interfaces */

/* Animations */
import { slideRightMessageMobile } from '../../../../Animations/animations';

export interface ConfirmModel {
	object 	: Object[]
	members : Object[]
}

@Component({
	selector:    'app-messages-mobile-modal',
	templateUrl: './messages-mobile-modal.component.html',
	styleUrls:   ['./messages-mobile-modal.component.scss'],
	animations:  [slideRightMessageMobile]
})
export class MessagesMobileModalComponent extends DialogComponent<ConfirmModel, string> implements OnInit {
	@ViewChild('scrollBottom') container: ElementRef;

	object 		: Object[]
	members 	: Object[]
	messages 	: Object[]
	message 	: string
	showDisplay : boolean = false
	animateState: string
	profile 	: Object

	constructor(dialogService: DialogService,
				private SocketService: SocketService,
				private ProfilesService: ProfilesService,
				private TokenService: TokenService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.addPrevenScroll();
	}

	ngOnInit() {
		this.messages 		= this.object['messages'];
		this.animateState 	= 'in'
		this.getUserProfile();
	}

	ngAfterViewChecked() {
		this.scrollToBottom();
	}

	getUserProfile() {
		this.ProfilesService.getProfile(this.TokenService.getToken().user.id).subscribe(res => {
			this.profile = res.profile;
		});
	}

	sendMessage() {
		//BACK
		if (this.message) {
			this.SocketService.sendMessage({
				message : this.replaceNewline(this.message),
				sender 	: this.profile['uid'],
				members : [this.members['uid'], this.profile['uid']],
				roomId 	: this.members['roomId'],
			});

			const object = {
				creation_date 	: new Date(),
				message 		: this.replaceNewline(this.message),
				sender 			: this.profile['uid'],
				name 			: this.profile['fullName'],
				sender_picture 	: this.profile['picture']
			}
			this.messages.push(object);
			this.message = ''
		}
	}

	hideModal() {
		this.animateState 	= 'out'
		this.removePreventScroll();
		this.close();
		setTimeout( () => {
			this.close();
		}, 200);
	}

	scrollToBottom(): void {
		try {
			this.container.nativeElement.scrollTop = this.container.nativeElement.scrollHeight;
		} catch (err) {
		}
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	/* DOM PREVENT SCROLL */
	addPrevenScroll() {
		this.document.body.classList.add('prevent-scroll');
	}

	removePreventScroll() {
		this.document.body.classList.remove('prevent-scroll');
	}
}



