import { Directive, ElementRef } from '@angular/core';

@Directive({
	selector: '[settingDiv]',
	host: {
		'(document:click)' 	: 'onClick($event)',
	},
})
export class SettingDirective {

	nativeElement : any
	target : any

	constructor(private el: ElementRef) {
		this.nativeElement = el.nativeElement
	}

	showNetworks() {
		if (this.nativeElement.querySelector('#sin-b') && this.nativeElement.querySelector('#sin-b').style.display !== 'block')
			this.nativeElement.querySelector('#sin-b').style.display = 'block'
		else
			this.hideNetworks();
	}

	hideNetworks() {
		if (this.nativeElement.querySelector('#sin-b'))
			this.nativeElement.querySelector('#sin-b').style.display = 'none';
	}

	onClick(event) {
		if (this.nativeElement.querySelector('#sin-i')) {
			if (this.nativeElement.querySelector('#sin-i').contains(event.target)) {
				this.showNetworks();
			} else
				this.hideNetworks();
		}
	}
}



