import { Component, OnInit, Inject } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from '@angular/platform-browser';

/* Services */
import { PicturesService } from '../../../../Services/Pictures/pictures.service';
import * as algoliasearch from 'algoliasearch'

/* Interfaces */

/* Animations */
import { mobileDialog } from '../../../../Animations/animations';

export interface ConfirmModel {
	statutes: Object
}

@Component({
	selector:    'app-search-mobile-modal',
	templateUrl: './search-mobile-modal.component.html',
	styleUrls:   ['./search-mobile-modal.component.scss'],
	providers: [PicturesService]
})
export class SearchMobileModalComponent extends DialogComponent<ConfirmModel, Object> implements OnInit {

	showModal 	: boolean = false
	searchName 	: string
	client      = algoliasearch('XQX5JQG4ZD', '8be065c7ce07e14525c377668a190cf8');
	PAndP 		: any
	searchResults : Object[]


	constructor(private PicturesService: PicturesService,
				dialogService: DialogService,
		@Inject(DOCUMENT) private document: any) {
		super(dialogService)
		this.hideBody();
		this.PAndP  	= this.client.initIndex('PAndP');
	}

	ngOnInit() {
	}

	onChange(event) {
		if (event)
			this.search(event);
	}

	search(word) {
		this.PAndP.search({ query: word, hitsPerPage: 10 }).then( res => {
			this.searchResults = res.hits;
		});
	}

	sortResults(content, content1, word) {

	}

	hideModalSuccess(status) {
		this.showBody();
		setTimeout(() => {
			this.result = status
			this.close();
		}, 100);
	}

	hideModal() {
		this.showBody();
		this.close();
	}

	/* DOM HIDE BODY */
	hideBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'none'
		this.document.body.querySelector('#w-app-body').style.display = 'none'
	}

	showBody() {
		this.document.body.querySelector('#w-app-header').style.display = 'block'
		this.document.body.querySelector('#w-app-body').style.display = 'block'
	}

	/* TRANSFORM */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}




