import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class TrackingService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	clickActivities(body: Object, token: Object, page_id: number, zone_id: number) : Observable<any> {
		let url,
			user_id;

		if (token && token['user'].id)
			user_id = token['user'].id
		else
			user_id = 0;


		url = 'http://localhost:3000/api/tracking/' + user_id + '/' + page_id + '/click/' + zone_id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	};

	viewActivities(body: Object, token: Object, page_id: number): Observable<any> {
		let url,
			user_id;

		if (token && token['user'].id)
			user_id = token['user'].id
		else
			user_id = 0;

		url = 'http://localhost:3000/api/tracking/' + user_id + '/' + page_id + '/view'

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	};
};



