import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Notification, NotificationService } from '../../../Services/notification.service';
import { Router, ActivatedRoute } from '@angular/router';

/* Services */
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';

@Component({
	selector:    'app-header-notif',
	templateUrl: './header-notif.component.html',
	styleUrls:   ['./header-notif.component.scss'],
	providers:   [TokenService]
})
export class HeaderNotifComponent implements OnInit {

	@Output() unread: EventEmitter<any> = new EventEmitter<any>();
	notifs 			: Notification[];
	unread_count 	: number;
	my_id 			: number = 0;

	constructor(private notifications: NotificationService,
		private PicturesService: PicturesService,
		private TokenService: TokenService,
		private ProfilesService: ProfilesService,
		private Router: Router) {
	}

	markAllAsRead(): void {
		this.unread_count = 0;
		this.notifs.map(n => { n.read = true; return n ; });
		this.notifications.markAsRead(null);
		this.getNotifications();
	}

	markAsRead(id: number): void {
		this.notifications.markAsRead(id);
		this.getNotifications();
	}

	ngOnInit() {
		this.notifs = [];
		this.unread_count = 0;
		this.getToken();
		this.getNotifications();
		this.getLiveNotifications();
	}

	getToken() {
		this.TokenService.getTokenStream()
		.subscribe(token => {
			this.my_id = token.user.id;
		});
	}

	getNotifications() {
		this.notifs = [];
		this.notifications.data()
		.subscribe(notification => {
			this.unread_count = 0;
			this.notifs = notification;
			this.notifs.forEach(n => {
				if (n.read == false || typeof n.read === 'undefined')
					this.unread_count++;
			});
			this.unread.emit(this.unread_count);
		});
	}

	getLiveNotifications() {
		this.notifications.dataStream()
		.subscribe(notification => {
			this.notifs.unshift(notification);
			this.unread_count++;
			this.unread.emit(this.unread_count);
		})
	}

	goToNotif(type, index, var1, var2) {
		this.markAsRead(index);
		if (type === 'profile') {
			this.Router.navigate(['/', var1]);
		} else {
			this.Router.navigate(['/project', var1, var2]);
		}
	}

	/* TRANSFORM FUNCTION */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}



