import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
	name: 'passwordMeter'
})
export class PasswordMeterPipe implements PipeTransform {

	transform(value: any, args?: any): any {
		if (!value) return args === 'class' ? 'pw-weak' : '';

		const strongPass = /^(?:(?=.*[a-z])(?:(?=.*[A-Z])(?=.*[\d\W])|(?=.*\W)(?=.*\d))|(?=.*\W)(?=.*[A-Z])(?=.*\d)).{8,}$/
		const mediumPass = /^(?=.{7,})(((?=.*[A-Z])(?=.*[a-z]))|((?=.*[A-Z])(?=.*[0-9]))|((?=.*[a-z])(?=.*[0-9]))).*$/

		if (strongPass.test(value))
			return args === 'class' ? 'pw-strong' : 'Strong' 
		else if (mediumPass.test(value))
			return args === 'class' ? 'pw-okey' : 'Medium'
		else
			return args === 'class' ? 'pw-weak' : 'Weak'
	}

}



