import { Component, OnInit } from '@angular/core';
import { SharedService } from '../Services/shared.service';
import { PrerenderService } from '../Services/prerender.service';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
	
	loginActive 	: boolean = false
	signUpActive 	: boolean = false
	mobileNavActive : boolean = false

	constructor(private SharedService: SharedService,
			private PrerenderService: PrerenderService) {
		this.SharedService.logActive$.subscribe( boolean => { this.loginActive = boolean; });
		this.SharedService.sigActive$.subscribe( boolean => { this.signUpActive = boolean; });
		this.SharedService.navMobileActive$.subscribe ( boolean => { this.mobileNavActive = boolean });
	}

	ngOnInit() {
		this.PrerenderService.readytoRender(4000);
	}
}



