import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';

@Injectable()
export class SharedService {

	private logActive 		= new Subject<boolean>();
	private sigActive 		= new Subject<boolean>();
	private postActive 		= new Subject<boolean>();
	private headerReload 	= new Subject<boolean>();
	private navMobileActive = new Subject<boolean>();
	private socketReload 	= new Subject<boolean>();
	private signupModal 	= new Subject<Object>();
	private postProject 	= new Subject<Object>();

	socketReload$ 	= this.socketReload.asObservable();
	logActive$ 		= this.logActive.asObservable();
	sigActive$ 		= this.sigActive.asObservable();
	signupModal$	= this.signupModal.asObservable();
	postActive$		= this.postActive.asObservable();
	headerReload$ 	= this.headerReload.asObservable();
	navMobileActive$= this.navMobileActive.asObservable();
	postProject$ 	= this.postProject.asObservable();

	setLoginStatus(status: boolean) {
		this.logActive.next(status);
	}

	setSignUpStatus(status: boolean) {
		this.sigActive.next(status);
	}

	setSignupModalIndex(index: number, lastIndex: number) {
		this.signupModal.next({index: index, lastIndex: lastIndex});
	}

	setPostProjectIndex(firstIndex: number, index: number, lastIndex: number) {
		this.postProject.next({
			firstIndex: firstIndex, index: index, lastIndex: lastIndex
		});
	}

	postActiveSignal(status: boolean) {
		this.postActive.next(status);
	}

	reloadHeader(status: boolean) {
		this.headerReload.next(status);
	}

	reloadSocket(status: boolean) {
		this.socketReload.next(status);
	}

	setSwitch(current) {
		if (current === 'Login') {
			this.logActive.next(false);
			this.sigActive.next(true);
		} else {
			this.logActive.next(true);
			this.sigActive.next(false);
		}

	}

	/* MOBILE */
	setNavMobileStatus(status: boolean) {
		this.navMobileActive.next(status);
	}

}



