import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { Router } from '@angular/router';

/* Services */
import { SharedService } from '../../../../Services/shared.service'
import { AuthenticationService, TokenService } from '../../../../Services/Authentication/auth-index';
import { ProfilesService } from '../../../../Services/Profiles/profiles.service';

/* Animations */
import { slideRightMobile } from '../../../../Animations/animations';

@Component({
	selector: 'app-navbar-mobile-modal',
	templateUrl: './navbar-mobile-modal.component.html',
	styleUrls: ['./navbar-mobile-modal.component.scss'],
	animations: [slideRightMobile],
	providers: [ProfilesService, AuthenticationService]
})
export class NavbarMobileModalComponent implements OnInit {

	animateState 	: string = 'on'
	log 			: boolean = false
	profile 		: Object
	showDisplay 	: boolean = false

	constructor(private SharedService: SharedService,
				private TokenService: TokenService,
				private ProfilesService: ProfilesService,
				private router: Router,
				private AuthenticationService: AuthenticationService,
				@Inject(DOCUMENT) private document: any) {
		this.addPrevenScroll();
	}

	ngOnInit() {
		this.getToken();
	}

	getToken() {
		this.TokenService.getTokenStream().subscribe(auth => {
			if (!auth) {
				this.log = false;
				this.showDisplay = true;
			} else {
				this.getProfile(auth);
			}
		});
	}

	getProfile(auth) {
		this.ProfilesService.getProfile(auth.user.id).subscribe(res => {
			this.profile = res.profile;
			this.log = true;
			this.showDisplay = true;
		});
	}

	logOut() {
		this.closeMobileNav();
		this.AuthenticationService.logout().subscribe(res => {
			this.TokenService.removeToken();
			this.router.navigate(['/']);
		},
		err => {
			this.TokenService.removeToken();
			this.router.navigate(['/']);
		});
	}

	closeMobileNav() {
		this.removePreventScroll();
		this.SharedService.setNavMobileStatus(false);
	}

	openLoginModal() {
		this.closeMobileNav();
		this.SharedService.setLoginStatus(true);
	}

	openSignupModal() {
		this.closeMobileNav();
		this.SharedService.setSignUpStatus(true);
	}

	/* DOM PREVENT SCROLL */
	addPrevenScroll() {
		setTimeout( () => {
			this.document.body.classList.add('prevent-scroll');
			if (this.log)
				this.document.body.classList.add('slide-body');
		}, 150);
	}

	removePreventScroll() {
		this.document.body.classList.remove('prevent-scroll');
		this.document.body.classList.remove('slide-body');
	}
}



